import React, { Component } from 'react'
import { Form, Input, Select, Tabs, Row, Col, message, Button, DatePicker } from 'antd'
import BreadcrumbLink from '../../../components/bread'
import './index.less'
import moment from 'moment'
import Api from '../../../api/index';
import { searchToParams } from '../../../utils/utils.js'
const dateFormat = 'YYYY/MM/DD HH:mm:ss'
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            const { bizId } = searchToParams();
            this.state = {
                bizId,
                selectByPrimaryKeyData: [],//基本信息
                pageListUserData: [],//审核人/审批人
                listFileTypesData: [],//文件类别
                listAllApplyTypesData: [],//申请类别
                listApplyDeptInFileModelData: [],//编写部门下拉
                listQMSDeptData: [],//申请部门/年审部门
                writtenData: [],//编写人
                listAllApplyReasonsData: [],//修改原因
                InputValue: '',
                pageData: {
                    pageSize: 100,
                    page: 1
                },
                breadcrumbData: [
                    { name: '文件管理' },
                    { name: '修改申请' },
                    { name: '申请修改' }
                ],
            }
        }
        componentDidMount() {
            this.selectByPrimaryKey()//基本信息
            this.pageListUser()//审核人/审批人
            this.listFileTypes()//文件类别下拉
            this.listAllApplyTypes()//申请类别
            this.listApplyDeptInFileModel()//编写部门下拉
            this.listQMSDept()//申请部门/年审部门
            this.listUsers()//编写人下拉
            this.listAllApplyReasons()//修改原因
        }

        selectByPrimaryKey = () => {
            const { bizId } = this.state
            Api.selectByPrimaryKey({
                bizId: bizId,
            }, (res) => {
                if (res.code === '200') {
                    this.setState({
                        selectByPrimaryKeyData: res.data
                    })
                }
            })
        }

        //审核人/审批人
        pageListUser = () => {
            const { pageData } = this.state
            Api.pageListUser({
                pageSize: pageData.pageSize,
                pageNo: pageData.page
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        pageListUserData: res.data.list,
                    })
                }
            })
        }
        //文件类别下拉
        listFileTypes = () => {
            Api.listFileTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listFileTypesData: res.data.list,
                    })
                }
            })
        }
        //申请类别
        listAllApplyTypes = () => {
            Api.listAllApplyTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyTypesData: res.data.list,
                    })
                }
            })
        }
        //编写部门下拉
        listApplyDeptInFileModel = () => {
            Api.listApplyDeptInFileModel({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listApplyDeptInFileModelData: res.data.list
                    })
                }
            })
        }
        //申请部门/年审部门
        listQMSDept = () => {
            Api.listQMSDept({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listQMSDeptData: res.data,
                    })
                }
            })
        }
        //编写人
        listUsers = () => {
            Api.listUsers({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        writtenData: res.data.list,
                    })
                }
            })
        }
        //修改原因
        listAllApplyReasons = () => {
            Api.listAllApplyReasons({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyReasonsData: res.data.list,
                    })
                }
            })
        }
        //点击 返回
        handleSearchPeople = () => {
            this.props.history.push('/fileManagement/annualReview')
        }
        //点击提交
        handleReset = () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                const { selectByPrimaryKeyData } = this.state
                console.log(values, '7777777777777')
                if (true) {
                    Api.insertFileYear({
                        fileId: selectByPrimaryKeyData.bizId,
                        versionEffect: values.versionEffect,
                        versionEffectInfo: values.versionEffectInfo == undefined ? values.versionEffectInfo : values.versionEffectInfo.trim(),
                        advancement: values.advancement,
                        advancementInfo: values.advancementInfo == undefined ? values.advancementInfo : values.advancementInfo.trim(),
                        format: values.format,
                        formatInfo: values.formatInfo == undefined ? values.formatInfo : values.formatInfo.trim(),
                        operability: values.operability,
                        operabilityInfo: values.operabilityInfo == undefined ? values.operabilityInfo : values.operabilityInfo.trim(),
                        applicability: values.applicability,
                        applicabilityInfo: values.applicabilityInfo == undefined ? values.applicabilityInfo : values.applicabilityInfo.trim(),
                    }, (res) => {
                        if (res.code === '200') {
                            message.success(res.message)
                            this.props.history.push('/fileManagement/annualReview')
                        }
                    })
                }
            });
        }

        //版本有效性
        handleOne = (value) => {
            console.log(value, '0000000')
            this.setState({
                versionEffectData: value
            })
        }
        //修订内容
        handleMaxRestoreUp = (e) => {
            console.log(this.inputdata.value)
            this.setState({
            });
        }
        render() {
            const {
                form,
            } = this.props;
            const {
                breadcrumbData,
                selectByPrimaryKeyData,
                pageListUserData,
                listFileTypesData,
                listAllApplyTypesData,
                listApplyDeptInFileModelData,
                listQMSDeptData,
                writtenData,
                listAllApplyReasonsData,
            } = this.state;
            const applyDateTwo = moment(selectByPrimaryKeyData.applyDate).format('YYYY/MM/DD HH:mm:ss');

            //审核人/审批人
            const pageListUser = pageListUserData ? pageListUserData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.dspName}</Option>)
            }) : null
            //文件类别
            const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
                return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
            }) : null
            //申请类别
            const listAllApplyTypes = listAllApplyTypesData ? listAllApplyTypesData.map(res => {
                return (<Option key={res.applyTypeId} values={res.applyTypeId}>{res.applyTypeName}</Option>)
            }) : null
            //编写部门
            const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
                return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
            }) : null
            //申请部门/年审部门
            const listQMSDept = listQMSDeptData ? listQMSDeptData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.deptName}</Option>)
            }) : null
            //编写人
            const writtenBy = writtenData ? writtenData.map(res => {
                return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
            }) : null
            //修改原因
            const listAllApply = listAllApplyReasonsData ? listAllApplyReasonsData.map(res => {
                return (<Option key={res.applyReasonId} values={res.applyReasonId}>{res.applyReasonName}</Option>)
            }) : null
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            return (
                <div className="applicationAmenndment">
                    <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                    {/* 一定要加p-content-wrapper */}
                    <div className='p-content-wrapper'>
                        <Form style={{ position: 'relative', top: '30px' }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('fileNo', {
                                            initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.fileNo : null
                                        })(
                                            <Input placeholder="请输入" disabled={true} autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="生效日期"
                                    >
                                        {getFieldDecorator('effectDate', {
                                            initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.effectDate : null
                                        })(
                                            <Input placeholder="请输入" disabled={true} autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('fileName', {
                                            initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.fileName : null
                                        })(
                                            <Input placeholder="请输入" disabled={true} autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="页码"
                                    >
                                        {getFieldDecorator('officePageSie', {
                                            initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.officePageSie : null
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                            initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.fileTypeName, key: selectByPrimaryKeyData.fileTypeId } : null,
                                        })(
                                            <Select
                                                disabled={true}
                                                labelInValue
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                            initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.applyDeptName, key: selectByPrimaryKeyData.applyDeptId } : null,
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                labelInValue
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('applyUserId', {
                                            initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.applyUserName, key: selectByPrimaryKeyData.applyUserId } : null,
                                        })(
                                            <Select
                                                disabled={true}
                                                labelInValue
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="版本/修订号"
                                    >
                                        {getFieldDecorator('fileVersion', {
                                            initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.fileVersion : null
                                        })(
                                            <Input placeholder="请输入" disabled={true} autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>

                            <div style={{ width: '90%', }}>
                                <div style={{ width: "50%", marginLeft: '50px' }}>
                                    <Row style={{ marginTop: '10px' }}>
                                        <Col span={6}>
                                            内容
                                </Col>
                                        <Col span={6}>
                                            结论
                                </Col>
                                        <Col span={6}>
                                            修订内容内容
                                </Col>
                                    </Row>
                                    <Row style={{ marginTop: '10px' }}>
                                        <Col span={6}>
                                            版本有效性：

                                </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('versionEffect', {
                                                })(
                                                    <Select
                                                        style={{ width: 80 }}
                                                        placeholder="请选择"
                                                        optionFilterProp="children"
                                                        onChange={this.handleOne}
                                                        onFocus={this.onFocus}
                                                        onBlur={this.onBlur}
                                                        filterOption={(input, option) =>
                                                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                                        }
                                                    >
                                                        <Option value={true}>是</Option>
                                                        <Option value={false}>否</Option>
                                                    </Select>
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('versionEffectInfo', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>

                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={6}>
                                            格式规范性：
                                    </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('format', {
                                                })(
                                                    <Select
                                                        style={{ width: 80 }}
                                                        placeholder="请选择"
                                                        optionFilterProp="children"
                                                        onChange={this.handleOne}
                                                        onFocus={this.onFocus}
                                                        onBlur={this.onBlur}
                                                        filterOption={(input, option) =>
                                                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                                        }
                                                    >
                                                        <Option value={true}>是</Option>
                                                        <Option value={false}>否</Option>
                                                    </Select>
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('formatInfo', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={6}>
                                            内容适用性：
                                    </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('applicability', {
                                                })(
                                                    <Select
                                                        style={{ width: 80 }}
                                                        placeholder="请选择"
                                                        optionFilterProp="children"
                                                        onChange={this.handleOne}
                                                        onFocus={this.onFocus}
                                                        onBlur={this.onBlur}
                                                        filterOption={(input, option) =>
                                                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                                        }
                                                    >
                                                        <Option value={true}>是</Option>
                                                        <Option value={false}>否</Option>
                                                    </Select>
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('applicabilityInfo', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={6}>
                                            评审人：
                                    </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('sss', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>

                                </div>
                                <div style={{ width: "50%", marginLeft: '50px' }}>
                                    <Row style={{ marginTop: '10px' }}>
                                        <Col span={6}>
                                            内容
                                </Col>
                                        <Col span={6}>
                                            结论
                                </Col>
                                        <Col span={6}>
                                            修订内容
                                </Col>
                                    </Row>
                                    <Row style={{ marginTop: '10px' }}>
                                        <Col span={6}>
                                            技术先进性：

                                        </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('advancement', {
                                                })(
                                                    <Select
                                                        style={{ width: 80 }}
                                                        placeholder="请选择"
                                                        optionFilterProp="children"
                                                        onChange={this.handleOne}
                                                        onFocus={this.onFocus}
                                                        onBlur={this.onBlur}
                                                        filterOption={(input, option) =>
                                                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                                        }
                                                    >
                                                        <Option value={true}>是</Option>
                                                        <Option value={false}>否</Option>
                                                    </Select>
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('advancementInfo', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={6}>
                                            执行可操作性：
                                    </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('operability', {
                                                })(
                                                    <Select
                                                        style={{ width: 80 }}
                                                        placeholder="请选择"
                                                        optionFilterProp="children"
                                                        onChange={this.handleOne}
                                                        onFocus={this.onFocus}
                                                        onBlur={this.onBlur}
                                                        filterOption={(input, option) =>
                                                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                                        }
                                                    >
                                                        <Option value={true}>是</Option>
                                                        <Option value={false}>否</Option>
                                                    </Select>
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('operabilityInfo', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={6}>
                                            其他：
                                    </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('ccs', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={6}>
                                            日期：
                                    </Col>
                                        <Col span={12}>
                                            <Form.Item
                                                style={{ marginBottom: 0 }}
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('endDate', {
                                                    initialValue: selectByPrimaryKeyData ? moment(applyDateTwo, 'YYYY/MM/DD HH:mm:ss') : null,
                                                    rules: [{
                                                        required: true, message: `请输入结束日期!`,
                                                    }
                                                    ],
                                                })(
                                                    <DatePicker format={dateFormat} onChange={this.onChangeTwo} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>

                                </div>
                            </div>

                            <Row style={{ marginTop: '10px' }}>
                                <Col span={6} offset={1}>
                                    <Button onClick={this.handleReset} style={{}}>提交</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginLeft: 20, }}>返回</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                </div>
            );
        }
    }
);
export default App