import React, { Component } from 'react'
import './index.less'
// import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import SearchForm from "./search-form"; // 搜索条件
import Table from "./table-list"; // 搜索条件
import DetailModel from './detailModal'; // 日志详情
class systemLog extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
              { name: '系统管理' },
              { name: '系统日志' }
            ],
            dataList: [],     // 操作列表数据
            loading: false,   // 列表是否正在加载中
            pageNo: 1,
            pageSize: 10,
            searchVal: {},
            detailShow: false,
            detailData: []
        }
    }
    componentDidMount() {
        this.queryTableList();
    }
    // 开始查询
    handleSearchBtn = (value) =>{
        console.log(value,'----search---');
        this.setState({
            searchVal: {...value},
            pageNo: 1
        },() => {
            this.queryTableList();
        })
    }
    // 查询试卷列表
    queryTableList = () =>{
        this.setState({
            loading: true
        },() => {
            const params = {
                ...this.state.searchVal,
                pageNo: this.state.pageNo,
                pageSize: this.state.pageSize
            }
            this.apis.pageListSysLogs(params,(res) => {
                if(res.code === '200') {
                    this.setState({
                        dataList: res.data.list,
                        total: res.data.total,
                        loading: false
                    })
                }
            })
        })
    }
    // 切换页码
    handleChangePage = (pageNo,pageSize) => {
        this.setState({
            pageNo,
            pageSize
        },() => {
            this.queryTableList();
        })
    }
    // 查看详情
    handleDetail = (code) => {
        this.apis.listQMSSysLogDetails({logId: code},(res)=> {
            if (res.code === '200') {
                this.setState({
                    detailShow: true,
                    detailData: res.data
                })
            }
        })
    }
    // 取消详情弹框
    handleCancel = () => {
        this.setState({
            detailShow: false,
            detailData: []
        })
    }
    render() {
        const { breadcrumbData, dataList, loading, total, pageNo, pageSize, detailShow, detailData } = this.state;
        return (
            <div className="systemLog">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 搜索条件 */}
                    <div className="p-search">
                        <SearchForm handleSearchBtn={this.handleSearchBtn} />
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList} 
                            handleChangePage={this.handleChangePage} 
                            handleDetail={this.handleDetail}
                            total={total}
                            pageNo={pageNo}
                            pageSize={pageSize}
                        />
                    </div>
                </div>
                <DetailModel
                    showModal={detailShow}
                    handleCancel={this.handleCancel}
                    data={detailData}
                />
            </div>
        )
    }
}
export default systemLog