import React, { Component } from 'react'
import './index.less'
import difference from 'lodash/difference'
import { Modal, Input, Form, Row, Col, Button, Select, Transfer, Table, Switch, Tag } from 'antd'
import api from '../../../api';
import Api from '../../../api/index'

const FormItem = Form.Item;
const Option = Select.Option;

const leftTableColumns = [
    {
        dataIndex: 'questionTitle',
        title: '试题名称',
        key: 'questionTitle	',
        render: (text, record, index) => {
            if (record.questionTitle) {
                return (
                    <div className='ellipsis'>{record.questionTitle}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
    {
        dataIndex: 'questionsTypeName',
        title: '试题类型',
        key: 'questionsTypeName	',
        render: (text, record, index) => {
            if (record.questionsTypeName) {
                return (
                    <div className='ellipsis'>{record.questionsTypeName}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
    {
        dataIndex: 'modifyUserName',
        title: '编写人',
        key: 'modifyUserName	',
        render: (text, record, index) => {
            if (record.modifyUserName) {
                return (
                    <div className='ellipsis'>{record.modifyUserName}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
    {
        dataIndex: 'modifyDate',
        title: '编写时间',
        key: 'modifyDate	',
        render: (text, record, index) => {
            if (record.modifyDate) {
                return (
                    <div className='ellipsis'>{record.modifyDate}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
];
const rightTableColumns = [
    {
        dataIndex: 'title',
        title: '试题名称',
    },
];

class addModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rightData: [],
            leftData: [],
            selectedKeys: [],   //选择的 key
            targetKeys: [],    //右边 data
            typeIdData: [],   //选择题型 id
            showSearch: false, //搜索
        }
    }
    componentDidMount() {
    }

    // 校验试卷类型不为空---不实用required
    checkPaperType = (rule, value, callback) => {
        if (value) {
            callback()
        } else {
            callback('请选择试卷类型')
        }
    }
    // 校验分值不为空---不实用required
    checkGroupCount = (rule, value, callback) => {
        if (value) {
            callback()
        } else {
            callback('请输入分值')
        }
    }

    onChangeTwo = (nextTargetKeys) => {
        console.log(nextTargetKeys,'11111111111 nextTargetKeys')
        this.setState({ targetKeys: nextTargetKeys });
    };

    //已选择的试题 
    listQuestionByQueryForExam = () => {
        const { typeIdData } = this.state
        Api.listQuestionByQueryForExam({
            questionIds: typeIdData
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    rightData: res.data
                })
            }
        })
    }
    //未选择试题
    handleChange = (value) => {
        console.log(value,'777')
        Api.listQuestionByQueryForExamNotChose({
            questionsTypeId: value.key
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    leftData: res.data
                })
            }
        })
    }
    //未选择试题
    handleChangeTwo = (value) => {
        Api.listQuestionByQueryForExamNotChose({
            questionsTypeId: value.key
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    leftData: res.data
                })
            }
        })
    }
    handle = () => {
        const { dataEdit } = this.props
        console.log()
        if (dataEdit && dataEdit.examQuestionTypeId) {
            Api.listQuestionByQueryForExamNotChose({
                questionsTypeId: dataEdit.examQuestionTypeId,
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        leftDataTwo: res.data
                    })
                }
            })
        } else {
            return
        }
    }
    onSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {
        this.setState({ selectedKeys: [...sourceSelectedKeys, ...targetSelectedKeys] });
      };
    render() {
        const { type, addModalShow, questionsType, isEditTwo, dataEdit, leftDataTwo } = this.props;
        const { getFieldDecorator } = this.props.form;
        const { rightData, leftData, selectedKeys, targetKeys, showSearch } = this.state;

        const TableTransfer = ({ leftColumns, rightColumns, ...restProps }) => (
            <Transfer {...restProps} showSelectAll={false}>
                {({
                    direction,
                    filteredItems,
                    onItemSelectAll,
                    onItemSelect,
                    selectedKeys: selectedKeys,
                }) => {
                    const columns = direction === 'left' ? leftColumns : rightColumns;

                    const rowSelection = {
                        onSelectAll(selected, selectedRows) {
                            const treeSelectedKeys = selectedRows
                                .map(({ key }) => key);
                            const diffKeys = selected
                                ? difference(treeSelectedKeys, selectedKeys)
                                :
                                difference(selectedKeys, treeSelectedKeys);
                            onItemSelectAll(diffKeys, selected);
                        },
                        onSelect({ key }, selected) {
                            onItemSelect(key, selected);
                        },
                        onChange: this.onSelectChange,
                        selectedKeys,
                    };

                    return (
                        <Table
                            rowSelection={rowSelection}
                            columns={columns}
                            dataSource={filteredItems}
                            size="small"
                            onRow={({ key, }) => ({
                                onClick: (event) => {
                                    onItemSelect(key, !selectedKeys.includes(key));
                                },
                            })}
                        />
                    );
                }}
            </Transfer>
        );

        return (
            <Modal
                className="addPaperModal"
                title={type === 0 ? '新增试题' : '修改试题'}
                visible={addModalShow}
                onOk={this.props.handleOk}
                onCancel={this.props.handleCancel}
            >
                <div className="search-wrap-one">
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                        <Row gutter={24}>
                            <Col span={12}>
                                {
                                    isEditTwo === "true" ?
                                        <FormItem label="试题类型">
                                            {getFieldDecorator('paperType', {
                                                initialValue: isEditTwo && dataEdit ? { key: dataEdit.examQuestionTypeId, label: dataEdit.questionTypeName } : null,
                                                rules: [
                                                    { validator: this.checkPaperType },
                                                ],
                                            })(
                                                <Select style={{ width: '180px' }} labelInValue placeholder="请选择" onChange={this.handleChangeTwo}>
                                                    {
                                                        questionsType.map(res => {
                                                            return <Option key={res.bizId} value={res.bizId}>{res.itemName}</Option>
                                                        })
                                                    }
                                                </Select>
                                            )}
                                        </FormItem>
                                        :
                                        <FormItem label="试题类型">
                                            {getFieldDecorator('paperType', {
                                                rules: [
                                                    { validator: this.checkPaperType },
                                                ],
                                            })(
                                                <Select style={{ width: '180px' }} labelInValue placeholder="请选择" onChange={this.handleChange}>
                                                    {
                                                        questionsType.map(res => {
                                                            return <Option key={res.bizId} value={res.bizId}>{res.itemName}</Option>
                                                        })
                                                    }
                                                </Select>
                                            )}
                                        </FormItem>
                                }
                            </Col>
                            <Col span={12}>
                                <FormItem label="分值">
                                    {getFieldDecorator('groupCount', {
                                        initialValue: isEditTwo && dataEdit ? dataEdit.questionsPoints : null,
                                        rules: [
                                            { pattern: /^([1-9][0-9]*)$/, message: '有效时间为正整数!' },
                                            { validator: this.checkGroupCount },
                                        ],
                                    })(
                                        <Input placeholder="请输入" suffix="分/题" />
                                    )}
                                </FormItem>
                            </Col>
                        </Row>
                    </Form>
                    <div className="search-wrap-two">
                        <span>待选择题</span>
                        <Select placeholder="请选择培训类型" style={{ 'width': '180px', 'marginLeft': '10px' }}>
                            <Option value={1}>1</Option>
                        </Select>
                        <Select placeholder="请选择题库类型" style={{ 'width': '180px', 'marginLeft': '10px' }}>
                            <Option value={1}>1</Option>
                        </Select>
                        <Input placeholder="请输入名称" style={{ 'width': '180px', 'marginLeft': '10px' }} />
                    </div>
                    {/* <div className="tableTransfer-wrap"> */}
                    <div style={{ width: "460px", overflowX: "auto" }}>
                            <TableTransfer
                                dataSource={leftData}
                                targetKeys={targetKeys}
                                showSearch={showSearch}
                                onChange={this.onChangeTwo}
                                onSelectChange={this.handleSelectChange}
                                filterOption={(inputValue, item) =>
                                    item.title.indexOf(inputValue) !== -1 || item.tag.indexOf(inputValue) !== -1
                                }
                                leftColumns={leftTableColumns}
                                rightColumns={rightTableColumns}
                            />
                        </div>
                    </div>
                {/* </div> */}
            </Modal>
        )
    }
}
export default Form.create()(addModal)