import React, { Component } from 'react'
import { Input, message, Table } from 'antd';
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
class testArrange extends Component {
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '考试管理' },
                { name: '参加考试' },
            ],
            loading: false,
            dataList: [], //列表 data
            pageSize: 10, //每页条数
            page: 1,      //页码
            showSizeChanger: true,
            isTextLook: true,
            isTextLookTwo: true
        }
    }
    componentDidMount() {
        this.queryTableList();
    }
    //设备列表分页
    handleTableChange = (page) => {
        this.setState({
            page,
        }, () => {
            this.queryTableList()
        })
    }
    // 查询试卷列表
    queryTableList = () => {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page,
        }
        Api.listExamUserPlanByQuery(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    dataList: res.data.list,
                    total: res.data.total,
                },
                    message.success(res.message)
                )
            } else {
                message.waring(res.message)
            }
        })
    }
    handleSearch = (event) => {
        console.log(event.target.value)
    }
    //点击开始考试 到考试页面
    handelDelete = (record) => {
        if (record.isFinish === true) {
            message.success("考试已结束,不能重复考试")
        } else {
            this.props.history.push(`/testManagement/testJoin/startExamTwo/?isTextLook=${this.state.isTextLook}&&examUserPlanModelId=${record.examUserPlanModelId}`)
        }
    }
    //点击查看 到查看页面
    handelDeleteLook = (record) => {
        if (record.isFinish === true) {
            this.props.history.push(`/testManagement/testJoin/startExamTwo/?isTextLookTwo=${this.state.isTextLookTwo}&&examUserPlanModelId=${record.examUserPlanModelId}`)
        } else {
            message.success("请先参加考试,再查看")
        }
    }
    render() {
        const { breadcrumbData, dataList, loading, showSizeChanger, total } = this.state;

        const columns = [
            {
                title: '试卷名称',
                dataIndex: 'examPlanName',
                key: 'examPlanName',
                render: (text, record, index) => {
                    if (record.examPlanName) {
                        return (
                            <div className='ellipsis'>{record.examPlanName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编制者',
                dataIndex: 'createUserName',
                key: 'createUserName',
                render: (text, record, index) => {
                    if (record.createUserName) {
                        return (
                            <div className='ellipsis'>{record.createUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '考试时间/分钟',
                dataIndex: 'examDuration',
                key: 'examDuration',
                render: (text, record, index) => {
                    if (record.examDuration) {
                        return (
                            <div className='ellipsis'>{record.examDuration}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '得分',
                dataIndex: 'baseTotalPoints',
                key: 'baseTotalPoints',
                render: (text, record, index) => {
                    if (record.baseTotalPoints) {
                        return (
                            <div className='ellipsis'>{record.baseTotalPoints}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '总分',
                dataIndex: 'totalPoints',
                key: 'totalPoints',
                render: (text, record, index) => {
                    if (record.totalPoints) {
                        return (
                            <div className='ellipsis'>{record.totalPoints}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '及格分数',
                dataIndex: 'passPoints',
                key: 'passPoints',
                render: (text, record, index) => {
                    if (record.passPoints) {
                        return (
                            <div className='ellipsis'>{record.passPoints}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '起始时间',
                dataIndex: 'startDate',
                key: 'startDate',
                render: (text, record, index) => {
                    if (record.startDate) {
                        return (
                            <div className='ellipsis'>{record.startDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '结束时间',
                dataIndex: 'endDate',
                key: 'endDate',
                render: (text, record, index) => {
                    if (record.endDate) {
                        return (
                            <div className='ellipsis'>{record.endDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            },
            {
                title: '操作',
                dataIndex: 'operation',
                width: 150,
                render: (text, record) =>
                    <div>
                        <a onClick={this.handelDelete.bind(this, record)} className="operation" >开始考试</a>
                        <a onClick={this.handelDeleteLook.bind(this, record)} className="operation" >查看</a>
                    </div>

            },
        ]

        const pagination = total > 10 ? {
            total: this.state.total,
            pageSize: this.state.pageSize,
            current: this.state.page,
            onChange: this.handleTableChange,
            showQuickJumper: true,
            showSizeChanger,
            onShowSizeChange: this.onShowSizeChange,
        } : false

        return (
            <div className="testJoin">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 新增或搜索 */}
                    <div className="btn-search">
                        考试名称：
                    <Input placeholder="请输入" onPressEnter={this.handleSearch.bind(this)} />
                    </div>
                    <div className="table-list">
                        <Table
                            columns={columns}
                            dataSource={dataList}
                            // pagination={pagination}
                            key="bizId"
                        ></Table>
                    </div>
                </div>
            </div>
        )
    }
}
export default testArrange