import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table, Tabs, Row, Col, message, Upload, Transfer, Button, } from 'antd'
import './index.less'
import Api from '../../../api/index';
import BreadcrumbLink from '../../../components/bread'
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
            }
        }
        componentDidMount() {
        }
        render() {
            const {
                form, examData, visible, onCreate, onCancel
            } = this.props;
            const {
                breadcrumbData
            } = this.state;

            const examDataForm = examData ? examData.map(res => {
                return <Option key={res.bizId} values={res.bizId}>{res.dspName}</Option>
            }) : null
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 },
            };

            return (
                <div className="wordForm">
                    <Modal
                        title="委托审批"
                        visible={visible}
                        onOk={onCreate}
                        onCancel={onCancel}
                    >
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                {/* <Col span={6}> */}
                                    <Form.Item
                                        {...formItemLayout}
                                        label="委托人"
                                    >
                                        {getFieldDecorator('bizId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {examDataForm}
                                            </Select>
                                        )}
                                    </Form.Item>
                                {/* </Col> */}
                            </Row>
                        </Form>
                    </Modal>
                </div>
            );
        }
    }
);
export default App