import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import { $override } from 'zrender/lib/core/util';
import api from '../../../api';


const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkColumns: [],
            isTest: true,
            isLook: true,
            pageSize: 10,
            page: 1,
            planData: [],//列表
            selectTime: [],
            showSizeChanger: true,//是否允许改变分页
            lookData: [],
            breadcrumbData: [
                { name: '培训管理' },
                { name: '我的培训' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '培训名称',
                key: 'planName',
                dataIndex: 'planName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.planName) {
                        return (
                            <div className='ellipsis'>{record.planName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训类别',
                key: 'startTime',
                dataIndex: 'startTime',
                render: (text, record, index) => {
                    if (record.startTime) {
                        return (
                            <div className='ellipsis'>{record.startTime}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件阅读',
                key: 'isFinishFile',
                dataIndex: 'isFinishFile',
                render: (text, record, index) => {
                    if (record.isFinishFile) {
                        return (
                            <div className='ellipsis'>{record.isFinishFile}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '阅读用时(分)',
                key: 'readTime',
                dataIndex: 'readTime',
                render: (text, record, index) => {
                    if (record.readTime) {
                        return (
                            <div className='ellipsis'>{record.readTime}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '完成考试',
                key: 'siteName',
                dataIndex: 'siteName',
                render: (text, record, index) => {
                    if (record.siteName) {
                        return (
                            <div className='ellipsis'>{record.siteName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '考试完成时间',
                key: 'wenjianleibie',
                dataIndex: 'wenjianleibie',
                render: (text, record, index) => {
                    if (record.wenjianleibie) {
                        return (
                            <div className='ellipsis'>{record.wenjianleibie}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '考试成绩',
                key: 'bianxiebumen',
                dataIndex: 'bianxiebumen',
                render: (text, record, index) => {
                    if (record.bianxiebumen) {
                        return (
                            <div className='ellipsis'>{record.bianxiebumen}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '及格分数',
                key: 'jigefenshu',
                dataIndex: 'jigefenshu',
                render: (text, record, index) => {
                    if (record.jigefenshu) {
                        return (
                            <div className='ellipsis'>{record.jigefenshu}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训结论',
                key: 'peixunjielun',
                dataIndex: 'peixunjielun',
                render: (text, record, index) => {
                    if (record.peixunjielun) {
                        return (
                            <div className='ellipsis'>{record.peixunjielun}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训状态',
                key: 'peixunzhuangtai',
                dataIndex: 'peixunzhuangtai',
                render: (text, record, index) => {
                    if (record.peixunzhuangtai) {
                        return (
                            <div className='ellipsis'>{record.peixunzhuangtai}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '开始时间',
                key: 'kaishishijain',
                dataIndex: 'kaishishijain',
                render: (text, record, index) => {
                    if (record.kaishishijain) {
                        return (
                            <div className='ellipsis'>{record.kaishishijain}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '结束时间',
                key: 'jieshushijian',
                dataIndex: 'jieshushijian',
                render: (text, record, index) => {
                    if (record.jieshushijian) {
                        return (
                            <div className='ellipsis'>{record.jieshushijian}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                width: 310,
                render: (text, record) => {
                    return (
                        <div>
                            {/* <a onClick={this.handelRevise}>文件阅读</a> &nbsp; */}
                            <a>
                                {/* {
                                    record.jieshushijian === "" ? */}
                                        <a onClick={this.handelDelete.bind(this, record)}>开始考试</a>
                                        {/* :
                                        null
                                } */}
                            </a>
                            &nbsp;
                            <a onClick={this.handelConfiguration.bind(this, record)}>查看</a>
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.listTrainExam()
        }
        )
    }
    //初始化 列表 数据
    listTrainExam() {
        const { pageSize, page } = this.state
        Api.listTrainExam({
            pageSize,
            pageNo: page
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list,
                    total: res.data.total,
                })
                message.success(res.message)
            } else {
                message.waring(res.message)
            }
        })
    }
    //设备列表分页
    handleTableChange = (page) => {
        this.setState({
            page,
        }, () => {
            this.listTrainExam()
        })
    }
    //点击开始考试 到考试页面
    handelDelete = ( record ) => {
        this.props.history.push(`/system/myTraining/startExamThree/?isTest=${this.state.isTest}&&bizId=${record.bizId}`)
    }
    //点击查看
    handelConfiguration = (record) => {
        if (record.isFinishQuestions === true) {
            this.props.history.push(
                `/system/myTraining/startExam/?isLook=${this.state.isLook}`,
                {
                    query: {
                        userId: record.userId,
                    },
                })
        } else {
            message.success("请先完成考试")
        }
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            showSizeChanger,
            total
        } = this.state;

        const pagination = total > 10 ? {
            total: this.state.total,
            pageSize: this.state.pageSize,
            current: this.state.page,
            onChange: this.handleTableChange,
            showQuickJumper: true,
            showSizeChanger,
            onShowSizeChange: this.onShowSizeChange,
        } : false
        return (
            <div className="maintenance">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* <Button type="primary" onClick={this.addMaintenance}>新增</Button> */}
                    <Table
                        style={{ marginTop: '20px' }}
                        columns={checkColumns}
                        dataSource={planData}
                        pagination={pagination}
                        key="bizId"
                    ></Table>
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner