import moment from 'moment';
import React,{ Component } from 'react'
import { Input, Form, Row, Col, Button, DatePicker, Select } from 'antd'
const { Option } = Select
const FormItem = Form.Item
const RangePicker = DatePicker.RangePicker

class FormListLog extends Component{
  constructor(props){
    super(props)
    this.state = {

    }
  }
    // 提交表单
    handleSubmit = () => {
        this.props.form.validateFields((err,values)=>{
            if(err){
                return false
            }
            const data = {};
            // 去除含有undefined的item
            Object.keys(values).forEach(res=>{
                if(values[res]){
                    data[res] = values[res]
                }
            })
            // 如果选择时间，解析字符串
            if (data.time) {
                data.beginTime = moment(values.time[0]).unix();
                data.endTime = moment(values.time[1]).unix();
                delete data.time;
            }else{
                data.beginTime = '';
                data.endTime = '';
            }
            this.props.handleSearchBtn(data);
        })
    }
    handleReset = () => {
        this.props.form.resetFields();
        this.props.handleSearchBtn({});
    }
    render(){
        const { getFieldDecorator } = this.props.form;
        return (
            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                <Row gutter={24}>
                    <Col span={8}>
                        <FormItem label="日期/时间">
                            {getFieldDecorator('time')(
                                <RangePicker allowClear showTime format="YYYY/MM/DD HH:mm:ss" />
                            )}
                        </FormItem>
                    </Col>
                    <Col span={8}>
                        <FormItem label="操作类型">
                            {getFieldDecorator('operationType')(
                                <Select allowClear placeholder="全部">
                                    <Option value="1">新增</Option>
                                    <Option value="2">修改</Option>
                                    <Option value="3">删除</Option>
                                    <Option value="4">其他</Option>
                                </Select>
                            )}
                        </FormItem>
                    </Col>
                    <Col span={8}>
                        <FormItem label="操作模块">
                            {getFieldDecorator('businessName')(
                                <Input placeholder="请输入" autoComplete='off' />
                            )}
                        </FormItem>
                    </Col>
                    <Col span={8}>
                        <FormItem label="操作用户">
                            {getFieldDecorator('operationUser')(
                                <Input placeholder="请输入" autoComplete='off' />
                            )}
                        </FormItem>
                    </Col>
                    <Col span={4}>
                        <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                        <Button type="primary"  onClick={this.handleSubmit} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                    </Col>
                </Row>
            </Form>
        )}
    }

export default Form.create()(FormListLog)

