import React, { Component } from 'react'
import { Modal, Checkbox } from 'antd';
import './index.less';
class jobAuthorization extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkedList: [],
            checkedAll: false,
            indeterminate: false
        }
    }
    componentWillReceiveProps(props) {
        if (props.visible !== this.props.visible && props.visible) {
            console.log(props)
            const length = props.checkedList && props.checkedList.length ? props.checkedList.length : 0;
            this.setState({
                checkedList: props.checkedList || [],
                indeterminate: length > 0 && length < props.data.length,
                checkedAll: length > 0 && length === props.data.length
            })
        }
    }
    // 确定弹框
    handleOk = () => {
        this.props.handleOk(this.state.checkedList);
    }
    // 取消弹框
    handleCancel = () => {
        this.props.handleCancel();
    }
    // 全选按钮
    onChange = (e) => {
        let { checkedList } = this.state;
        if (e.target.checked) {
            checkedList = this.props.data.map(item => {
                return item.bizId
            })
        } else {
            checkedList = [];
        }
        this.setState({
            checkedList,
            indeterminate: !e.target.checked,
            checkedAll: e.target.checked
        })
    }
    // 点击选项卡选中
    handleClick = (e) => {
        const { id, index } = e.target.dataset;
        const { checkedList } = this.state;
        if (index === '-1') {
            checkedList.push(id);
        } else {
            checkedList.splice(index, 1)
        }
        this.setState({
            checkedList
        }, () => {
            const length = this.state.checkedList.length
            this.setState({
                indeterminate: length > 0 && length < this.props.data.length,
                checkedAll: length > 0 && length === this.props.data.length
            })
        })
    }
    render() {
        const { checkedList, checkedAll, indeterminate } = this.state;
        const { visible, isEditTwo } = this.props;
        const _title = isEditTwo ? "人员配置" : "职位授权"
        return (
            <Modal
                title={_title}
                visible={visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                width={588}
                className="jobAuthorization"
            >
                <div className="body-wrap">
                    {/* 头部操作 */}
                    <div className="top-header">
                        <span>职位</span>
                        <Checkbox indeterminate={indeterminate} checked={checkedAll} onChange={this.onChange}>全选</Checkbox>
                    </div>
                    {/* 列表 */}
                    <div className="list-wrap">
                        {
                            this.props.data.map(item => {
                                return <li
                                    className={`item ellipsis ${checkedList.indexOf(item.bizId) > -1 ? 'checked' : ''}`}
                                    key={item.bizId}
                                    data-id={item.bizId}
                                    data-index={checkedList.indexOf(item.bizId)}
                                    onClick={this.handleClick}
                                >
                                    {
                                        isEditTwo ?
                                            item.dspName
                                            :
                                            item.jobName
                                    }
                                </li>
                            })
                        }
                    </div>
                </div>
            </Modal>
        )
    }
}
export default jobAuthorization