import React, { Component } from 'react'
import { Form, Input, Modal, Select, Checkbox } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const CheckboxGroup = Checkbox.Grou
const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
            }
        }
        componentDidMount() {
        }

        render() {
            const {
                visible, onCancel, onCreate, form, pageListQMSRolesData, checkedList, onChange, onCheckAllChange, checkAll
            } = this.props;
            const {
            } = this.state;

            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            return (
                <Modal
                    style={{ width: '500px' }}
                    visible={visible}
                    title={'授权'}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form style={{ width: "450px", overflowX: "auto", height: '450px', overflowY: "auto" }}>
                        <div style={{ borderBottom: '1px solid #E9E9E9' }}>
                            <Checkbox
                                indeterminate={this.state.indeterminate}
                                onChange={onCheckAllChange}
                                checked={checkAll}
                            >
                                全选
                           </Checkbox>
                        </div>
                        <br />
                        <Checkbox.Group
                            options={pageListQMSRolesData}
                            // defaultValue={this.state.checkedList}
                            value={checkedList}
                            onChange={onChange}
                        />
                    </Form>
                </Modal>
            );
        }
    }
);
export default App