import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Card, Select, Statistic } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../api/index'
import BreadcrumbLink from '../../components/bread'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import axios from 'axios'
import { searchToParams } from '../../utils/utils.js'
const FormItem = Form.Item
const { Option } = Select;
const Search = Input.Search;
const { RangePicker } = DatePicker;
const { Countdown } = Statistic
class onBanner extends Component {
    constructor(props) {
        super(props)
        const { bizId, isLook, isTextLookTwo, isTestTwo, isTextLook, examUserPlanModelId } = searchToParams();
        this.state = {
            msg: "",
            isLook,
            isTextLook,
            isTextLookTwo,
            bizId, //我的培训 考试
            examUserPlanModelId,
            handleLookData: [],//查看 data
            handleStartExamData: [],//考试管理 开始考试 data
            lookData: [],
            breadcrumbData: [
                { name: '考试管理' },
                { name: '参加考试' },
                { name: '考试' }
            ],
        }
    }
    componentDidMount() {
        this.handleStartExam()//开始考试/查看考试
    }
    //倒计时结束的回调
    onFinish() {
        this.handleConfirmation()
    }
    //考试管理 开始考试
    handleStartExam = () => {
        const { examUserPlanModelId, isTextLookTwo } = this.state

        isTextLookTwo ? // 是否为 查看试卷
            Api.lookExam({
                examUserPlanModelId: examUserPlanModelId ? examUserPlanModelId : []
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        handleLookData: res.data
                    })
                }
            })
            :
            Api.startExam({
                examUserPlanModelId: examUserPlanModelId ? examUserPlanModelId : []
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        handleStartExamData: res.data
                    })
                }
            })
    }
    //点击退出
    handleLoginOut = () => {
        this.props.history.push(`/testManagement/testJoin`)
    }
    //点击 确认交卷
    handleConfirmation = () => {
        const { handleStartExamData } = this.state
        console.log(handleStartExamData, '555555555555 handleStartExamData')
        this.props.form.validateFieldsAndScroll((errors, values) => {
            console.log(values, '2222222222 values')
            // const questionsTwo = handleStartExamData ? handleStartExamData.examQuestionTypes.map((val, i) => { //examQuestionTypes
            //     return val
            // }) : [] 
            // const questions = questionsTwo ? questionsTwo.map((val, i) => {
            //     return (
            //         val.questionTypeName === "单选题" ?
            //             val.questionsPoints
            //             :
            //             val.questionTypeName === "多选题" ?
            //                 val.questionsPoints
            //                 :
            //                 null
            //     )
            // }) : []
            if (true) {
                const { examUserPlanModelId, handleStartExamData } = this.state
                Api.submitExamAnswer({
                    examUserPlanId: examUserPlanModelId,
                    examPlanId: handleStartExamData.examPlanId,
                    examId: handleStartExamData.examId
                }, (res) => {
                    if (res.code === "200") {
                        message.success(res.message)
                        this.props.history.push('testManagement/testJoin')
                    }
                })
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            isLook,
            isTextLook,
            breadcrumbData,
            handleStartExamData,
            handleLookData,
            isTextLookTwo
        } = this.state;

        const topic = handleStartExamData && handleStartExamData.examQuestionTypes ? handleStartExamData.examQuestionTypes.map((val, i) => {  //开始考试
            return (
                <div className="startExammTwo">
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span>{val.questionTypeName} ({val.questionsPoints} 分/题)</span>
                        </div>
                    </div>
                    {
                        val.questionTypeName === "单选题" ?
                            <span>
                                {val.questionForPreview.map((values, index) => {
                                    return <Card title={<div>第 {Number(index + 1)}题：{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                        <p>A:{values.questionA}</p>
                                        <p>B:{values.questionB}</p>
                                        <p>C:{values.questionC}</p>
                                        <p>D:{values.questionD}</p>
                                        <p>E:{values.questionE}</p>
                                        <p>F:{values.questionF}</p>
                                        <p>
                                            <FormItem
                                                {...formItemLayout}
                                                label="我的答案:"
                                            >
                                                {
                                                    getFieldDecorator([values.bizId], {
                                                    })(
                                                        <Select onChange={this.handleSelect} style={{ width: '100px' }} placeholder="请选择">
                                                            <Option value="A">A</Option>
                                                            <Option value="B">B</Option>
                                                            <Option value="C">C</Option>
                                                            <Option value="D">D</Option>
                                                            <Option value="E">E</Option>
                                                            <Option value="F">F</Option>
                                                        </Select>
                                                    )
                                                }
                                            </FormItem>
                                        </p>
                                    </Card>
                                })}
                            </span>
                            :

                            val.questionTypeName === "多选题" ?
                                <span>
                                    {val.questionForPreview.map((values, index) => {
                                        return <Card title={<div>第 {Number(index + 1)}题：{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                            <p>A:{values.questionA}</p>
                                            <p>B:{values.questionB}</p>
                                            <p>C:{values.questionC}</p>
                                            <p>D:{values.questionD}</p>
                                            <p>E:{values.questionE}</p>
                                            <p>F:{values.questionF}</p>
                                            <p>
                                                <FormItem
                                                    {...formItemLayout}
                                                    label="我的答案:"
                                                >
                                                    {
                                                        getFieldDecorator([values.bizId])(
                                                            <Select onChange={this.handleStartExamData} mode="multiple" style={{ width: '100px' }} placeholder="请选择">
                                                                <Option value="A">A</Option>
                                                                <Option value="B">B</Option>
                                                                <Option value="C">C</Option>
                                                                <Option value="D">D</Option>
                                                                <Option value="E">E</Option>
                                                                <Option value="F">F</Option>
                                                            </Select>
                                                        )
                                                    }
                                                </FormItem>
                                            </p>
                                        </Card>
                                    })}
                                </span>
                                :
                                null

                    }

                </div>
            )
        }) : ''

        const look = handleLookData && handleLookData.examQuestionTypes ? handleLookData.examQuestionTypes.map((val, i) => {    //试卷 预览
            return (
                <div>
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span>{val.questionTypeName} (5分/题)</span>
                        </div>
                    </div>

                    {
                        val.questionTypeName === "单选题" ?
                            <span>
                                {val.questionForPreview.map((values, index) => {
                                    return <Card title={<div>第 {Number(index + 1)}题：{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                        <p>A:{values.questionA}</p>
                                        <p>B:{values.questionB}</p>
                                        <p>C:{values.questionC}</p>
                                        <p>D:{values.questionD}</p>
                                        <p>E:{values.questionE}</p>
                                        <p>F:{values.questionF}</p>
                                        <p>我的答案：{values.userAnswer}  得分：{values.userPoints} </p>
                                    </Card>
                                })}
                            </span>
                            :
                            val.questionTypeName === "多选题" ?
                                <span>
                                    {val.questionForPreview.map((values, index) => {
                                        return <Card title={<div>第 {Number(index + 1)}题：{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                            <p>A:{values.questionA}</p>
                                            <p>B:{values.questionB}</p>
                                            <p>C:{values.questionC}</p>
                                            <p>D:{values.questionD}</p>
                                            <p>E:{values.questionE}</p>
                                            <p>F:{values.questionF}</p>
                                            <p>我的答案：{values.userAnswer} 得分：{values.userPoints} </p>
                                        </Card>
                                    })}
                                </span>
                                :
                                null

                    }

                </div>
            )
        }) : ''

        const dataTime = this.state.handleStartExamData && this.state.handleStartExamData.examDuration ? this.state.handleStartExamData.examDuration : [] //考试的 总时长
        const deadline = Date.now() + 1000 * 60 * dataTime; // 倒计时 value
        return (
            <div className="startTheExam">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="information">
                        <div style={{ float: "right" }}>
                            {
                                dataTime > 60 ?
                                    <Countdown onFinish={this.onFinish} title="剩余时间：" value={deadline} format="H 时 m 分 s 秒" />
                                    :
                                    dataTime > 1440 ?
                                        < Countdown onFinish={this.onFinish} title="剩余时间：" value={deadline} format="D 天 H 时 m 分 s 秒" />
                                        :
                                        < Countdown onFinish={this.onFinish} title="剩余时间：" value={deadline} format=" m 分 s 秒" />
                            }
                        </div>
                        <div>
                            <span>考试人员：</span><span>{handleStartExamData.dspName}</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>试卷名称：</span><span>{handleStartExamData.examName}</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>考试时间：{handleStartExamData.examDuration} 分钟</span>
                            <span style={{ marginLeft: '20px' }}> 总分： {handleStartExamData.totalPoints} 分</span>
                            <span style={{ marginLeft: '20px' }}> 合格：{handleStartExamData.passPoints}分</span>
                        </div>
                    </div>
                    {
                        isTextLook ?
                            topic
                            :
                            isTextLookTwo ?
                                look
                                :
                                ''
                    }
                </div>
                <div className="bottom-btn">
                    <Button style={{ float: 'right' }} onClick={this.handleLoginOut}>退出</Button>
                    {
                        isTextLook ?
                            <Button style={{ float: 'right', marginRight: '20px' }} type="primary" onClick={this.handleConfirmation}>确认交卷</Button>
                            :
                            null
                    }
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner