import React, { Component } from 'react'
import { Button, Pagination, Table, Form, Row, Col, Input, Select, } from 'antd';
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
// import SearchForm from "./search-form"; // 搜索条件
const { Option } = Select
const FormItem = Form.Item
class onBanner extends Component {
  constructor(props) {
    super(props)
    this.state = {
      breadcrumbData: [
        { name: '考试管理' },
        { name: '试卷管理' }
      ],
      dataList: [],     // 试卷列表数据
      searchValue: {},  // 查询
      loading: false,   // 列表是否正在加载中
      dataList: [],     // 列表数据
      pageSize: 10,     // 分页Size
      showSizeChanger: true,
      page: 1,        // 分页页码
      planData: [],
      isEdit: true,
    }
  }
  componentDidMount() {
    this.pageListForReceive()
  }
  //初始化 列表 数据
  pageListForReceive() {
    const { pageSize, page } = this.state
    Api.pageListExamTotal({
      pageSize,
      pageNo: page
    }, (res) => {
      if (res.code === "200") {
        this.setState({
          total: res.data.total,
          planData: res.data.list
        })
      }
    })
  }
  //设备列表分页
  handleTableChange = (page) => {
    this.setState({
      page,
    }, () => {
      this.handleSearchPeople()
    })
  }
  //点击 重置
  handleReset = () => {
    this.props.form.resetFields();
    this.pageListForReceive()
  }
  //点击查询
  handleSearchPeople = () => {
    this.setState({
      page: 1,
    }, () => {
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (true) {
          const { pageSize, page } = this.state;
          this.setState({
            deviceLoading: true
          })
          Api.pageListExamTotal({
            pageSize,
            pageNo: page,
            dspName: values.dspName == undefined ? values.dspName : values.dspName.trim(),
            deptName: values.deptName == undefined ? values.deptName : values.deptName.trim(),
            trainName: values.trainName == undefined ? values.trainName : values.trainName.trim(),
            pass: values.pass,
          }, (res) => {
            if (res.code === '200') {
              this.setState({
                page,
                total: res.data.total,
                planData: res.data.list
              })
            }
          })
        }
      });
    })
  }

  //导出
  exportFiles = () => {
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (true) {
        Api.downloadFile('/train/exportExam', {
          dspName: values.dspName == undefined ? null : values.dspName,
          trainName: values.trainName == undefined ? null : values.trainName.trim(),
          passStr: values.passStr == undefined ? null : values.passStr.trim(),
        })
      }
    })
  }
  render() {
    const { breadcrumbData, dataList, loading, total, showSizeChanger, planData } = this.state;
    const { getFieldDecorator } = this.props.form;

    const pagination = total > 10 ? {
      total: this.state.total,
      pageSize: this.state.pageSize,
      current: this.state.page,
      onChange: this.handleTableChange,
      showQuickJumper: true,
      showSizeChanger,
      onShowSizeChange: this.onShowSizeChange,
    } : false

    const columns = [
      {
        title: '姓名',
        dataIndex: 'dspName',
        key: 'dspName',
        render: (text, record, index) => {
          if (record.dspName) {
            return (
              <div className='ellipsis'>{record.dspName}</div>
            )
          } else {
            return (
              <div>-</div>
            )
          }
        },
      }, {
        title: '部门',
        dataIndex: 'deptName',
        key: 'deptName',
        render: (text, record, index) => {
          if (record.deptName) {
            return (
              <div className='ellipsis'>{record.deptName}</div>
            )
          } else {
            return (
              <div>-</div>
            )
          }
        },
      }, {
        title: '考试名称',
        dataIndex: 'trainName',
        key: 'trainName',
        render: (text, record, index) => {
          if (record.trainName) {
            return (
              <div className='ellipsis'>{record.trainName}</div>
            )
          } else {
            return (
              <div>-</div>
            )
          }
        },
      }, {
        title: '得分',
        dataIndex: 'pointStr',
        key: 'pointStr',
        render: (text, record, index) => {
          if (record.pointStr) {
            return (
              <div className='ellipsis'>{record.pointStr}</div>
            )
          } else {
            return (
              <div>-</div>
            )
          }
        },
      }, {
        title: '及格/总分',
        dataIndex: 'totalPoints',
        key: 'totalPoints',
        render: (text, record, index) => {
          if (record.totalPoints) {
            return (
              <div className='ellipsis'>{record.totalPoints}</div>
            )
          } else {
            return (
              <div>-</div>
            )
          }
        },
      }, {
        title: '完成时间',
        dataIndex: 'finishTime',
        key: 'finishTime',
        render: (text, record, index) => {
          if (record.finishTime) {
            return (
              <div className='ellipsis'>{record.finishTime}</div>
            )
          } else {
            return (
              <div>-</div>
            )
          }
        },
      }, {
        title: '是否合格',
        dataIndex: 'pass',
        key: 'pass',
        render: (text, record, index) => {
          if (record.pass === true) {
            return (
              <div className='ellipsis'>是</div>
            )
          } if (record.pass === false) {
            return (
              <div className='ellipsis'>否</div>
            )
          } else {
            return (
              <div>-</div>
            )
          }
        },
      }]
    return (
      <div className="testStatistics">
        <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
        {/* 一定要加p-content-wrapper */}
        <div className='p-content-wrapper'>
          {/* 搜索条件 */}
          <div className="p-search">
            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
              <Row gutter={24}>
                <Col span={8}>
                  <FormItem label="输入查询">
                    {
                      getFieldDecorator('dspName')(<Input placeholder="请输入用户姓名或账号" autoComplete='off' />)
                    }
                  </FormItem>
                </Col>
                <Col span={8}>
                  <FormItem label="部门">
                    {
                      getFieldDecorator('deptName')(<Input placeholder="请输入" autoComplete='off' />)
                    }
                  </FormItem>
                </Col>
                <Col span={8}>
                  <FormItem label="培训名称">
                    {
                      getFieldDecorator('trainName')(<Input placeholder="请输入" autoComplete='off' />)
                    }
                  </FormItem>
                </Col>
                <Col span={8}>
                  <FormItem label="考试结果">
                    {
                      getFieldDecorator('passStr')(
                        <Select placeholder="请选择">
                          <Option value={true}>及格</Option>
                          <Option value={false}>不及格</Option>
                        </Select>
                      )
                    }
                  </FormItem>
                </Col>
                <Col span={4}>
                  <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                  <Button type="primary" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                </Col>
              </Row>
            </Form>
          </div>
          <div className="addBtn">
            <Button>重新发起考试</Button>
            <Button onClick={this.exportFiles} className="download"><a>导出</a></Button>
          </div>
          <div className="table-list">
            <Table
              columns={columns}
              dataSource={planData}
              pagination={pagination}
              key="sadas"
            ></Table>
          </div>
        </div>
      </div>
    )
  }
}
const Banner = Form.create()(onBanner);
export default Banner