import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Switch, Select, Modal } from 'antd';
import {
    HashRouter as Router, Route, Link, withRouter,
} from 'react-router-dom';
import './index.less'
import JobAuthorization from '../../../components/jobAuthorization'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import moment from 'moment'
import BreadcrumbLink from '../../../components/bread'
import FromMaintenance from './maintenanceForm';
import FromConfiguration from './configurationForm';
import FromReportForms from './reportForms';
const { Option } = Select;
const { TextArea } = Input;
const Search = Input.Search;
const { RangePicker } = DatePicker;
const dateFormat = 'YYYY/MM/DD HH:mm:ss'
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            selectedRowKeys: [],
            selectedRowKeysOn: [],
            selectTime: [],
            showSizeChanger: true,//是否允许改变分页
            userDetail: [],
            listTypeDetailsData: [],//培训类别
            jobModellShow: false, //职位授权
            listExamForPlanData: [],//考试试卷
            jobList: [],
            checkColumns: [],
            pageData: {
                pageSize: 100,
                page: 1
            },
            disabled: true,
            pageSize: 10,
            page: 1,
            maintenanceVisible: false,//新增 弹窗
            maintenanceVisibleTwo: false, //修改 弹窗
            configurationVisible: false,//文件配置 弹窗
            reportFormsVisible: false,//报表 弹窗
            isEdit: false,//判断是否是编辑弹窗
            planDataFiles: [],//待选文件
            planDataFilesed: [],//已选文件
            selectTime: [],
            isEditTwo: true, //人员配置
            planData: [],
            reportForm: [],//报表
            reportFormTwo: [],
            reportFormTwoData: "",
            dataFiles: {
                pageSize: 10,
                page: 1,
            },
            dataFilesed: {
                pageSize: 10,
                page: 1,
            },
            breadcrumbData: [
                { name: '培训管理' },
                { name: '培训配置' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '类别',
                key: 'trainTypeName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.trainTypeName) {
                        return (
                            <div className='ellipsis'>{record.trainTypeName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训名称',
                key: 'trainName',
                render: (text, record, index) => {
                    if (record.trainName) {
                        return (
                            <div className='ellipsis'>{record.trainName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '备注',
                key: 'trainDescription',
                render: (text, record, index) => {
                    if (record.trainDescription) {
                        return (
                            <div className='ellipsis'>{record.trainDescription}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '起始日期',
                key: 'startDate',
                render: (text, record, index) => {
                    if (record.startDate) {
                        return (
                            <div className='ellipsis'>{record.startDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '结束日期',
                key: 'endDate',
                render: (text, record, index) => {
                    if (record.endDate) {
                        return (
                            <div className='ellipsis'>{record.endDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '启用',
                key: 'isStart',
                render: (text, record, index) => {
                    if (record.isStart === false) {
                        return (
                            <Switch onChange={this.handleTurnOn} disabled={this.state.disabled} checkedChildren="开" unCheckedChildren="关" />
                        )
                    } if (record.isStart === true) {
                        return (
                            <Switch onChange={this.handleTurnOn} disabled={this.state.disabled} checkedChildren="开" unCheckedChildren="关" defaultChecked />
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                    // return (
                    //     <div>
                    //         <Switch onChange={this.handleTurn} checkedChildren="开" unCheckedChildren="关" defaultChecked />
                    //     </div>
                    // )
                },
            }, {
                title: '创建日期',
                key: 'gmtCreated',
                render: (text, record, index) => {
                    if (record.gmtCreated) {
                        return (
                            <div className='ellipsis'>{record.gmtCreated}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // fixed: 'right',
                width: 310,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.handelRevise.bind(this, record)}>编辑</a> &nbsp;
                            <a onClick={this.handelDelete.bind(this, record)}>删除</a> &nbsp;
                            <a onClick={this.handelConfiguration.bind(this, record)}>文件配置</a> &nbsp;
                            <a onClick={this.handleJobModelShow.bind(this, record)}>人员配置</a> &nbsp;
                            {
                                record.isStart === false ?
                                    <a onClick={this.handelRunning.bind(this, record)}>启动</a>
                                    :
                                    null
                            } &nbsp;
                            <a onClick={this.handelReportForms.bind(this, record)}>报表</a>
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.pageListTrains()
            this.listTypeDetails()//培训类别
            this.queryJobList()
            this.listExamForPlan()//考试试卷
        }
        )
    }
    onChange = (e, dateString) => {
        this.setState({
            selectStartTime: dateString
        })
    }
    onChangeTwo = (e, dateString) => {
        console.log(e, dateString, '4444444444');
        this.setState({
            selectEndTime: dateString
        })
    }

    //培训类别
    listTypeDetails = () => {
        Api.listTypeDetails({
            itemsId: 4
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    listTypeDetailsData: res.data.list
                })
            }
        })
    }
    //考试试卷
    listExamForPlan = () => {
        Api.listExamForPlan({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listExamForPlanData: res.data
                })
            }
        })
    }
    //设备列表分页
    handleTableChange = (page) => {
        this.setState({
            page,
        }, () => {
            this.pageListTrains()
        })
    }
    //初始化 列表 数据
    pageListTrains() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page
        }
        Api.pageListTrains(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                },
                )
            } else {
                message.warning(res.message)
            }
        })
    }
    //启用
    handelRunning = (record) => {
        Api.updateTrain({
            bizId: record.bizId,
            isStart: true,
        }, (res) => {
            if (res.code === "200") {
                message.success(res.message)
            }
            this.pageListTrains()
        })
    }

    //点击启用开关
    handleTurnOff = (checked) => {
        this.setState({
            checked: true
        })
    }
    handleTurnOn = (checked) => {
        this.setState({
            checked: false
        })
    }
    //点击新增 打开弹窗
    addMaintenance = () => {
        this.setState({
            maintenanceVisible: true,
        })
    }
    //新增 弹窗 点击取消
    maintenanceCancel = () => {
        this.setState({
            maintenanceVisible: false,
        })
    }
    //编辑 弹窗 点击取消
    maintenanceCancelTwo = () => {
        // const form = this.formRefRevise.props.form;
        // form.resetFields();
        this.setState({
            maintenanceVisibleTwo: false,
            isEdit: false,
        })
    }
    saveFormRefRevise = (formRefRevise) => {
        this.formRefRevise = formRefRevise;
    }

    //新增 确定
    maintenanceCreate = () => {
        const { selectStartTime, selectEndTime } = this.state
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (true) {
                Api.addTrain({
                    trainTypeId: values.trainTypeId,
                    trainName: values.trainName == undefined ? values.trainName : values.trainName.trim(),
                    trainDescription: values.trainDescription == undefined ? values.trainDescription : values.trainDescription.trim(),
                    examName: values.examName == undefined ? values.examName : values.examName.trim(),
                    examId: values.examId,
                    totalPoint: values.totalPoint == undefined ? values.totalPoint : values.totalPoint.trim(),
                    fileTypeIdName: values.fileTypeIdName,
                    passPoint: values.passPoint == undefined ? values.passPoint : values.passPoint.trim(),
                    readFen: values.readFen == undefined ? values.readFen : values.readFen.trim(),
                    startDate: selectStartTime,
                    endDate: selectEndTime,
                }, (res) => {
                    if (res.code === '200') {
                        this.setState({
                            maintenanceVisible: false,
                            isEdit: false,
                        },
                            message.success("新增成功")
                        )
                        this.pageListTrains()
                    }
                })
            }
        });
    }
    //编辑 弹窗 点击确定
    maintenanceCreateTwo = (bizId) => {
        const { selectStartTime, selectEndTime } = this.state
        const form = this.formRefRevise.props.form;
        form.validateFields((err, value) => {
            if (err) {
                return;
            }
            Api.updateTrain({
                bizId: bizId,
                trainTypeId: value.trainTypeId.key,
                trainName: value.trainName == undefined ? value.trainName : value.trainName.trim(),
                trainDescription: value.trainDescription == undefined ? value.trainDescription : value.trainDescription.trim(),
                examName: value.examName == undefined ? value.examName : value.examName.trim(),
                examId: value.examId.key,
                totalPoint: value.totalPoint == undefined ? value.totalPoint : value.totalPoint.trim(),
                fileTypeIdName: value.fileTypeIdName,
                passPoint: value.passPoint,
                readFen: value.readFen == undefined ? (value.readFen).toString() : (value.readFen).toString().trim(),
                startDate: value.startDate._i ? value.startDate._i : selectStartTime,
                endDate: value.endDate._i ? value.endDate._i : selectEndTime,
            }, (res) => {
                if (res.code === '200') {
                    this.setState({
                        maintenanceVisibleTwo: false,
                        isEdit: false,
                    },
                        message.success("修改成功")
                    )
                    this.pageListTrains()
                }
            })
        });
    }
    //点击编辑 打开弹窗
    handelRevise = (record) => {
        if (record.isStart === true) {
            message.warning("已启动培训项目不能编辑")
        } else {
            let _bizId = '';
            _bizId = record.bizId

            Api.getTrainConfigStatement({
                bizId: record.bizId
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        reportFormTwo: res.data,
                        reportFormTwoData: res.data.startDate
                    })
                }
            })
            this.setState({
                bizId: record.bizId,
                maintenanceVisibleTwo: true,
                isEdit: true,
            })
        }
    }
    //点击删除
    handelDelete = (record) => {
        Api.deleteTrain({
            bizId: record.bizId,
        }, (res) => {
            if (res.code === "200") {
                this.pageListTrains()
                message.success("删除成功")
            } else {
                message.warning(res.message)
            }
        }
        )

    }
    //点击文件配置 打开弹窗
    handelConfiguration = (record) => {
        if (record.isStart === true) {
            message.warning("已启动培训项目不能文件配置")
        } else {
            this.setState({
                configurationVisible: true,
                filesTrainId: record.bizId
            })
            this.pageListTrainFilesSelect(record)
            this.pageListTrainFilesSelected(record)
        }
    }
    //待选文件
    pageListTrainFilesSelect = (record) => {
        const { dataFiles } = this.state
        Api.pageListTrainFilesSelect({
            pageSize: dataFiles.pageSize,
            pageNo: dataFiles.page,
            trainId: record.bizId,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planDataFiles: res.data.list
                })
            }
        })

    }
    //已选文件
    pageListTrainFilesSelected = (record) => {
        const { dataFilesed } = this.state
        Api.pageListTrainFilesSelected({
            pageSize: dataFilesed.pageSize,
            pageNo: dataFilesed.page,
            trainId: record.bizId,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planDataFilesed: res.data.list,
                    recordId: record.bizId
                })
            }
        })
    }

    //文件配置弹窗 点击取消
    configurationCancel = () => {
        this.setState({
            configurationVisible: false,
        })
    }
    //文件配置弹窗 点击确定
    configurationCreate = () => {
        this.setState({
            configurationVisible: false,
        })
    }
    //点击 报表 打开弹窗
    handelReportForms = (record) => {
        Api.getTrainConfigStatement({
            bizId: record.bizId
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    reportForm: res.data
                })
            }
        })
        this.setState({
            reportFormsVisible: true,
        })
    }
    //报表 点击取消
    reportFormsCancel = () => {
        this.setState({
            reportFormsVisible: false,
        })
    }
    //报表 点击确定
    reportFormsCreate = () => {
        this.setState({
            reportFormsVisible: false,
        })
    }
    // 职位授权 -- 显示弹框
    handleJobModelShow = (record) => {
        if (record.isStart === true) {
            message.warning("已启动培训项目不能人员配置")
        } else {
            this.setState({
                jobModellShow: true,
                userDetail: record,
                isEditTwo: true
            })
        }
    }
    // 部门弹框--关闭
    handleJobCancle = () => {
        this.setState({
            jobModellShow: false,
            userDetail: {}
        })
    }
    // 部门弹框--确定部门设置
    handleJobOk = (jobIdList) => {
        const params = {
            trainId: this.state.userDetail.bizId,
            userIdList: jobIdList
        }
        Api.addTrainUser(params, (res) => {
            if (res.code === '200') {
                message.success('职位设置成功！');
                this.handleJobCancle();
                this.pageListTrains()
            }
        })
    }
    // 查询人员列表
    queryJobList = () => {
        const { pageData } = this.state
        Api.pageListUser({
            pageSize: pageData.pageSize,
            pageNo: pageData.page
        }, (res) => {
            if (res.code === '200') {
                this.setState({
                    jobList: res.data.list,
                })
            }
        })
    }
    onSelectChange = (selectedRowKeys, selectedRows) => {
        const filesId = selectedRows.map(v => {
            return v.bizId
        })
        this.setState({
            selectedRowKeys,
            filesId,
        });
    }

    onSelectChangeOn = (selectedRowKeys, selectedRows) => {
        const filesIded = selectedRows.map(v => {
            return v.bizId
        })
        this.setState({
            selectedRowKeys,
            filesIded,
        });
    }
    //添加
    addTrainFile = () => {
        const { filesId } = this.state
        Api.addTrainFile({
            trainId: this.state.filesTrainId,
            fileIdList: filesId
        }, (res) => {
            if (res.code = "200") {
                message.success(res.message)
            }
            window.location.reload()
        })
    }
    //删除
    deleteTrainFile = () => {
        const { filesIded } = this.state
        Api.deleteTrainFile({
            fileIdList: filesIded,
        }, (res) => {
            if (res.code = "200") {
                message.success(res.message)
            }
            this.pageListTrainFilesSelected()
        })
    }

    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            maintenanceVisible,
            configurationVisible,
            isEdit,
            reportFormsVisible,
            trainId,
            planDataFiles,
            planDataFilesed,
            recordId,
            filesTrainId,
            reportForm,
            listTypeDetailsData,
            listExamForPlanData,
            reportFormTwo,
            userDetail,
            reportFormTwoData,
            maintenanceVisibleTwo,
            bizId,
            jobList,
            jobModellShow,
            total,
            isEditTwo,
            showSizeChanger,
            selectedRowKeys
        } = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
        };

        const rowSelectionOn = {
            selectedRowKeys,
            onChange: this.onSelectChangeOn,
        };

        const startTime = moment(reportFormTwo.startDate).format('YYYY/MM/DD HH:mm:ss');
        const endTime = moment(reportFormTwo.endDate).format('YYYY/MM/DD HH:mm:ss');
        //培训类别
        const listTypeDetails = listTypeDetailsData ? listTypeDetailsData.map(res => {
            return (<Option key={res.bizId} values={res.bizId}>{res.itemName}</Option>)
        }) : null
        //考试试卷
        const listExamForPlan = listExamForPlanData ? listExamForPlanData.map(res => {
            return (<Option key={res.examId} values={res.examId}>{res.examName}</Option>)
        }) : null
        const pagination = total > 10 ? {
            total: this.state.total,
            pageSize: this.state.pageSize,
            current: this.state.page,
            onChange: this.handleTableChange,
            showQuickJumper: true,
            showSizeChanger,
            onShowSizeChange: this.onShowSizeChange,
        } : false
        const _title = isEdit ? "培训配置--编辑" : "培训配置--添加"
        return (
            <div className="maintenance">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <Button type="primary" onClick={this.addMaintenance}>新增</Button>
                    <Table
                        style={{ marginTop: '20px' }}
                        columns={checkColumns}
                        dataSource={planData}
                        pagination={pagination}
                        key="bizId"
                    ></Table>
                    {/* 文件配置 */}
                    <FromConfiguration
                        visible={configurationVisible}
                        onCancel={this.configurationCancel}
                        onCreate={this.configurationCreate}
                        planDataFiles={planDataFiles}
                        planDataFilesed={planDataFilesed}
                        recordId={recordId}
                        filesTrainId={filesTrainId}
                        // filesFunction={this.pageListTrainFilesSelect}
                        // filesFunctioned={this.pageListTrainFilesSelected}
                        addTrainFile={this.addTrainFile}
                        rowSelection={rowSelection}
                        rowSelectionOn={rowSelectionOn}
                    />
                    {/* 人员授权 */}
                    <JobAuthorization
                        ref="DeptModel"
                        visible={jobModellShow}
                        data={jobList}
                        name={userDetail.dspName}
                        checkedList={userDetail.jobIdListStr}
                        handleCancel={this.handleJobCancle}
                        handleOk={this.handleJobOk}
                        isEditTwo={isEditTwo}
                    />
                    {/* 报表 */}
                    <FromReportForms
                        visible={reportFormsVisible}
                        onCancel={this.reportFormsCancel}
                        onCreate={this.reportFormsCreate}
                        reportForm={reportForm}
                    />
                    {/* 编辑 */}
                    <FromMaintenance
                        wrappedComponentRef={this.saveFormRefRevise}
                        visible={maintenanceVisibleTwo}
                        onCancel={this.maintenanceCancelTwo}
                        onCreate={this.maintenanceCreateTwo.bind(this, bizId)}
                        reportFormTwo={reportFormTwo}
                        isEdit={isEdit}
                        onChange={this.onChange}
                        onChangeTwo={this.onChangeTwo}
                    />

                    {/* {
                        isEdit ? */}

                    {/* : */}

                    <Modal
                        visible={maintenanceVisible}
                        title={_title}
                        okText="确定"
                        cancelText='取消'
                        // wrappedComponentRef={(saveForm) => this.saveForm = saveForm}
                        onCancel={this.maintenanceCancel}
                        onOk={this.maintenanceCreate}
                        maskClosable={false}
                    >
                        <Form>
                            <Form.Item
                                label={'培训类别'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('trainTypeId', {
                                    // initialValue: isEdit && reportFormTwo ? { label: reportFormTwo.trainTypeName, key: reportFormTwo.trainTypeId } : null,
                                    rules: [{
                                        required: true, message: `请选择培训类别!`,
                                    }],
                                })(
                                    <Select
                                        allowClear
                                        // labelInValue
                                        placeholder="请选择"
                                        onChange={this.queryEleList}
                                        getPopupContainer={triggerNode => triggerNode.parentNode}
                                    >
                                        {listTypeDetails}
                                    </Select>
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'培训名称'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('trainName', {
                                    rules: [{
                                        required: true, message: `请输入培训名称!`,
                                    }],
                                })(
                                    <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'考试试卷'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('examId', {
                                    rules: [{
                                        required: true, message: `请选择考试试卷!`,
                                    }],
                                })(
                                    <Select
                                        allowClear
                                        // labelInValue
                                        placeholder="请选择"
                                        onChange={this.queryEleList}
                                        getPopupContainer={triggerNode => triggerNode.parentNode}
                                    >
                                        {listExamForPlan}
                                    </Select>
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'及格分数'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('passPoint', {
                                    rules: [
                                        {
                                            required: true,
                                            message: `请输入及格分数!`,
                                            pattern: new RegExp(/^[0-9]*$/),
                                            message: '只能输入数字'
                                        },
                                        // {
                                        //     max: 100,
                                        //     message: "项目名称不得超过100个字"
                                        // }
                                    ],
                                })(
                                    <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'应培训时间'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('readFen', {
                                    rules: [
                                        {
                                            required: true,
                                            message: `请选输入应培训时间!`,
                                            pattern: new RegExp(/^[0-9]*$/),
                                            message: '只能输入数字'
                                        },
                                        // {
                                        //     max: 200,
                                        //     message: "内容不得超过200个字"
                                        // }
                                    ],
                                })(
                                    <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'培训内容'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('trainDescription', {
                                    rules: [{
                                        required: true, message: `请输入培训内容!`,
                                    }],
                                })(
                                    <TextArea style={{ width: 340, height: 100 }} placeholder="请输入" />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'开始日期'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('startDate', {
                                    rules: [{
                                        required: true, message: `请输入开始日期!`,
                                    }],
                                })(
                                    < DatePicker format={dateFormat} onChange={this.onChange} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                )}
                            </Form.Item>
                            <Form.Item
                                style={{ marginBottom: 0 }}
                                {...formItemLayout}
                                label="结束日期"
                            >
                                {getFieldDecorator('endDate', {
                                    rules: [{
                                        required: true, message: `请输入结束日期!`,
                                    }
                                    ],
                                })(
                                    <DatePicker format={dateFormat} onChange={this.onChangeTwo} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                )}
                            </Form.Item>
                        </Form>
                    </Modal>

                    {/* } */}

                </div>
            </div >
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner