import React, { Component } from 'react'
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import { Input, Form, Row, Col, Radio, Table, Button } from 'antd';
import AddModal from "./addModal";
import api from '../../../api';
import { searchToParams } from '../../../utils/utils.js'

const FormItem = Form.Item

class addPaper extends Component {
    constructor(props) {
        super(props)
        const { examId, isEdit } = searchToParams();
        this.state = {
            type: 0,             // 0、新增 1、修改
            addModalShow: false, // 新增或修改弹框的显隐
            questionsType: [],   //试题类型 data
            paperDetail: {
                checked: true    //
            },
            examId,              //试卷id
            dataAdd: [],         //新增弹窗 data
            isEdit,              //是否为 编辑
            isEditTwo: true,     //是否为修改弹窗
            breadcrumbData: [
                { name: '考试管理' },
                { name: '考试安排' },
                { name: '新增试卷' }
            ],
        }
    }
    componentDidMount() {
        this.listTypeDetails()
        this.getExamByExamId()
    }
    getExamByExamId = () => {
        const { isEdit, examId } = this.state
        if (isEdit) {
            Api.getExamByExamId({
                examId: examId
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        getExamByExamIdData: res.data
                    })
                }
            })
        } else {
            return
        }
    }
    // 试卷类型的选中状态切换
    handleChangeRadio = () => {
        const { paperDetail } = this.state;
        this.setState({
            paperDetail: { ...paperDetail, checked: !paperDetail.checked }
        })
    }
    //s试题类型
    listTypeDetails = () => {
        Api.listTypeDetails({
            itemsId: 5
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    questionsType: res.data.list
                })
            }
        })
    }
    // 新增 打开弹框
    handleAddBtn = () => {
        const arr = this.state.questionsType
        this.setState({
            type: 0,
            addModalShow: true,
            questionsType: arr
        })
    }
    //修改 打开弹窗
    handleDefite = (val) => {
        console.log(val, '999999999999')
        const { isEditTwo } = this.state
        const arr = this.state.questionsType
        this.setState({
            isEditTwo: true,
            type: 0,
            addModalShow: true,
            questionsType: arr,
            dataEdit: val
        })
        Api.listQuestionByQueryForExamNotChose({
            questionsTypeId: val.examQuestionTypeId
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    leftDataTwo: res.data
                })
            }
        })
    }
    // 关闭弹框
    handleCancel = () => {
        this.setState({
            addModalShow: false,
            dataEdit: []
        })
    }
    // 确认弹框
    handleOk = () => {
        const form = this.formRefRevise.props.form;
        form.validateFields((err, value) => {
            console.log(value, err, '88888888888888')
            if (err) {
                return;
            }
            this.setState({
                addModalShow: false,
                dataEdit: [],
                dataAdd: value,
            })

        })

    }
    saveFormRefRevise = (formRefRevise) => {
        this.formRefRevise = formRefRevise;
    }

    render() {
        const { breadcrumbData, dataSource, type, addModalShow, questionsType, paperDetail, getExamByExamIdData, isEdit, isEditTwo, dataEdit, leftDataTwo } = this.state;

        const { getFieldDecorator } = this.props.form;
        const columns = [{
            title: '试卷名称',
            dataIndex: 'questionTitle',
            key: 'questionTitle',
            render: (text, record, index) => {
                if (record.questionTitle) {
                    return (
                        <div className='ellipsis'>{record.questionTitle}</div>
                    )
                } else {
                    return (
                        <div>-</div>
                    )
                }
            },
        }, {
            title: '试题类型',
            dataIndex: 'questionsTypeName',
            key: 'questionsTypeName',
            render: (text, record, index) => {
                if (record.questionsTypeName) {
                    return (
                        <div className='ellipsis'>{record.questionsTypeName}</div>
                    )
                } else {
                    return (
                        <div>-</div>
                    )
                }
            },
        }, {
            title: '编写人',
            dataIndex: 'modifyUserName',
            key: 'modifyUserName',
            render: (text, record, index) => {
                if (record.modifyUserName) {
                    return (
                        <div className='ellipsis'>{record.modifyUserName}</div>
                    )
                } else {
                    return (
                        <div>-</div>
                    )
                }
            },
        }, {
            title: '编写时间',
            dataIndex: 'modifyDate',
            key: 'modifyDate',
            render: (text, record, index) => {
                if (record.modifyDate) {
                    return (
                        <div className='ellipsis'>{record.modifyDate}</div>
                    )
                } else {
                    return (
                        <div>-</div>
                    )
                }
            },
        }]

        const getExamByExam = getExamByExamIdData && getExamByExamIdData.examQuestionTypes ? getExamByExamIdData.examQuestionTypes.map((val, i) => {
            console.log(val, '1111111111')
            return (
                <div>
                    <div className="right-btn">
                        {
                            val.questionTypeName === "单选题" ?
                                <span>
                                    <span onClick={this.handleDefite.bind(this, val)} >修改</span>
                                    <span>删除</span>
                                </span>
                                :
                                val.questionTypeName === "多选题" ?
                                    <span>
                                        <span onClick={this.handleDefite.bind(this, val)} >修改</span>
                                        <span>删除</span>
                                    </span>
                                    :
                                    null

                        }
                    </div>
                    < Table
                        className="tableClass"
                        dataSource={
                            val.examQuestions
                        }
                        columns={columns}
                        pagination={false}
                        rowKey={record => record.bizId}
                    />
                </div>
            )
        }) : null
        return (
            <div className="addPaper" >
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper' >
                    <div className="conster-wrap">
                        <p className="title">试卷详情</p>
                        <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                            <Row gutter={24}>
                                <Col span={12}>
                                    <FormItem label="试卷名称">
                                        {getFieldDecorator('examName', {
                                            initialValue: isEdit ? (getExamByExamIdData && getExamByExamIdData.examName ? getExamByExamIdData.examName : null) : null,
                                            rules: [{ required: true, message: '请输入试卷名称' }],
                                        })(
                                            <Input maxLength={60} placeholder="请输入" autoComplete='off' />
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={12}>
                                    <FormItem label="试卷类型">
                                        {getFieldDecorator('isPeixun', {
                                            // initialValue: paperDetail.isPeixun,
                                        })(
                                            <Radio checked={paperDetail.isPeixun} onClick={this.handleChangeRadio}>勾选为培训试题（培训试题不采用简述题和问答题）</Radio>
                                        )}
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row gutter={24}>
                                <Col span={12}>
                                    <FormItem label="时长">
                                        {getFieldDecorator('examDuration', {
                                            initialValue: isEdit ? (getExamByExamIdData && getExamByExamIdData.examDuration ? getExamByExamIdData.examDuration : null) : null,
                                            rules: [
                                                { required: true, message: '请输入时长' },
                                                { pattern: /^([1-9][0-9]*)$/, message: '时长为正整数!' }
                                            ]
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' suffix="分钟" />
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={12}>
                                    <FormItem label="总分">
                                        0
                                    </FormItem>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div className="dashed"></div>
                    <div className="conster-wrap">
                        <p className="title lineHeight">试题配置</p>
                        <div className="table-wrap">
                            <div className="table-title">
                                <div className="left-title">
                                    <span className="testType">单选题</span>
                                    <span>分值：2分</span>
                                    <span>题目数：2</span>
                                    <span>合集：2分</span>
                                </div>
                            </div>
                            {
                                isEdit ?
                                    getExamByExam
                                    :
                                    ''
                            }
                        </div>
                        <div className="table-wrap-blue" onClick={this.handleAddBtn}>新增试题</div>
                    </div>
                </div>
                <div className="bottom-btn">
                    <Button type="primary">提 交</Button>
                    <Button style={{ marginLeft: '10px'}}>退 出</Button>
                </div>
                <AddModal
                    type={type}
                    wrappedComponentRef={this.saveFormRefRevise}
                    questionsType={questionsType}
                    addModalShow={addModalShow}
                    handleOk={this.handleOk}
                    handleCancel={this.handleCancel}
                    isEditTwo={isEditTwo}
                    getExamByExamIdData={getExamByExamIdData}
                    dataEdit={dataEdit}
                    leftDataTwo={leftDataTwo}
                />
            </div >
        )
    }
}
export default Form.create()(addPaper)