import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Card, Select, Statistic } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../api/index'
import BreadcrumbLink from '../../components/bread'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import axios from 'axios'
import { searchToParams } from '../../utils/utils.js'
const FormItem = Form.Item
const { Option } = Select;
const Search = Input.Search;
const { RangePicker } = DatePicker;
const { Countdown } = Statistic
class onBanner extends Component {
    constructor(props) {
        super(props)
        const { isTest, bizId, isLook, isTestTwo, examUserPlanModelId } = searchToParams();
        this.state = {
            msg: "",
            isTest,
            isLook,
            bizId, //我的培训 考试
            examUserPlanModelId,
            beginTrainExamData: [], //培训管理 开始考试
            lookData: [],
            breadcrumbData: [
                { name: '考试管理' },
                { name: '参加考试' },
                { name: '考试' }
            ],
        }
    }
    componentDidMount() {
        this.handleStartExamTwo()//
    }
    //我的培训 开始考试
    handleStartExamTwo = () => {
        const { bizId } = this.state
        Api.beginTrainExam({
            bizId: bizId
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    beginTrainExamData: res.data
                })
            }
        })
    }
    //点击退出
    handleLoginOut = () => {
        this.props.history.push('testManagement/testJoin')
    }
    //点击 确认交卷
    handleConfirmation = () => {
        this.props.form.validateFieldsAndScroll((err, values) => {
            console.log(values, '22222222222')
            let _obj = {}
            _obj["questions"] = values
            console.log(_obj, '44444444444')
            if (true) {
                const { bizId, beginTrainExamData } = this.state
                Api.submitExamAnswer({
                    bizId: bizId,
                    examPlanId: beginTrainExamData.examPlanId,
                    examId: beginTrainExamData.examId
                }, (res) => {
                    if (res.code === "200") {
                        this.props.history.push('testManagement/testJoin')
                        message.success(res.message)
                    }
                })
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            data,
            lookData,
            isTest, isLook, breadcrumbData, beginTrainExamData
        } = this.state;
        let { bool, days, hours, minutes, seconds } = this.state
        const topic = beginTrainExamData && beginTrainExamData.examQuestionTypes ? beginTrainExamData.examQuestionTypes.map((val, i) => {
            return (
                <div>
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span>{val.questionTypeName} ({val.questionsPoints}分/题)</span>
                        </div>
                    </div>

                    {
                        val.questionTypeName === "单选题" ?
                            <span>
                                {val.questionForPreview.map((values, index) => {
                                    return <Card title={<div>第 {Number(index + 1)}题：{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                        <p>A:{values.questionA}</p>
                                        <p>B:{values.questionB}</p>
                                        <p>C:{values.questionC}</p>
                                        <p>D:{values.questionD}</p>
                                        <p>E:{values.questionE}</p>
                                        <p>F:{values.questionF}</p>
                                        <p>
                                            <FormItem
                                                {...formItemLayout}
                                                label="我的答案:"
                                            >
                                                {
                                                    getFieldDecorator('pass' + [values.bizId])(
                                                        <Select onChange={this.beginTrainExamData} style={{ width: '100px' }} placeholder="请选择">
                                                            <Option value="A">A</Option>
                                                            <Option value="B">B</Option>
                                                            <Option value="C">C</Option>
                                                            <Option value="D">D</Option>
                                                            <Option value="E">E</Option>
                                                            <Option value="F">F</Option>
                                                        </Select>
                                                    )
                                                }
                                            </FormItem>
                                        </p>
                                    </Card>
                                })}
                            </span>
                            :

                            val.questionTypeName === "多选题" ?
                                <span>
                                    {val.questionForPreview.map((values, index) => {
                                        return <Card title={<div>第 {Number(index + 1)}题：{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                            <p>A:{values.questionA}</p>
                                            <p>B:{values.questionB}</p>
                                            <p>C:{values.questionC}</p>
                                            <p>D:{values.questionD}</p>
                                            <p>E:{values.questionE}</p>
                                            <p>F:{values.questionF}</p>
                                            <p>
                                                <FormItem
                                                    {...formItemLayout}
                                                    label="我的答案:"
                                                >
                                                    {
                                                        getFieldDecorator('pass' + [values.bizId])(
                                                            <Select onChange={this.beginTrainExamData} mode="multiple" style={{ width: '100px' }} placeholder="请选择">
                                                                <Option value="A">A</Option>
                                                                <Option value="B">B</Option>
                                                                <Option value="C">C</Option>
                                                                <Option value="D">D</Option>
                                                                <Option value="E">E</Option>
                                                                <Option value="F">F</Option>
                                                            </Select>
                                                        )
                                                    }
                                                </FormItem>
                                            </p>
                                        </Card>
                                    })}
                                </span>
                                :
                                null

                    }

                </div>
                // val.questionForPreview.map((values) => {
                //     // console.log(values, '1111111111111111')
                //     <Card title={<div>第一题：这里是题目内容</div>} style={{ width: 300, marginTop: '50px' }}>
                //         <p>A:{values.questionA}</p>
                //         <p>B:{values.questionB}</p>
                //         <p>C:{values.questionC}</p>
                //         <p>D:{values.questionD}</p>
                //         <p>E:{values.questionE}</p>
                //     </Card>
                // })
            )
        }) : ''

        const dataTime = this.state.beginTrainExamData && this.state.beginTrainExamData.examDuration ? this.state.beginTrainExamData.examDuration : []
        console.log(dataTime, '5555555555')
        const deadline = Date.now() + 1000 * 60 * dataTime; // Moment is also OK

        return (
            <div className="startTheExam">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="information">
                        <div style={{ float: "right" }}>
                            {
                                dataTime > 60 ?
                                    <Countdown onFinish={this.onFinish} title="剩余时间：" value={deadline} format="H 时 m 分 s 秒" />
                                    :
                                    dataTime > 1440 ?
                                        < Countdown onFinish={this.onFinish} title="剩余时间：" value={deadline} format="D 天 H 时 m 分 s 秒" />
                                        :
                                        < Countdown onFinish={this.onFinish} title="剩余时间：" value={deadline} format=" m 分 s 秒" />
                            }
                        </div>
                        <div>
                            <span>考试人员：</span><span>{beginTrainExamData.dspName}</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>试卷名称：</span><span>{beginTrainExamData.examName}</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>考试时间：{beginTrainExamData.examDuration} 分钟</span>
                            <span style={{ marginLeft: '20px' }}> 总分： {beginTrainExamData.totalPoints} 分</span>
                            <span style={{ marginLeft: '20px' }}> 合格：{beginTrainExamData.passPoints}分</span>
                        </div>
                    </div>
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span>单选题 (2分/题)</span>
                        </div>
                    </div>
                    {topic}
                </div>
                <div className="bottom-btn">
                    <Button style={{ float: 'right' }} onClick={this.handleLoginOut}>退出</Button>
                    <Button style={{ float: 'right', marginRight: '20px' }} type="primary" onClick={this.handleConfirmation}>确认交卷</Button>
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner