import React, { Component } from 'react'
import { Button, Input, Table, Popconfirm, message, DatePicker, Modal, Form, Col, Row, Select } from 'antd';
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import AddFormFromWrap from './addForm';
import JobAuthorization from '../../../components/jobAuthorization'
const { RangePicker } = DatePicker;
const { Option } = Select;
const { TextArea } = Input;
const dateFormat = 'YYYY/MM/DD HH:mm:ss'
// import Table from "./table-list"; // 搜索条件
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '考试管理' },
                { name: '考试安排' }
            ],
            pageSize: 10,
            showSizeChanger: true,
            userDetail: [],
            page: 1,
            pageData: {
                pageSize: 100,
                page: 1
            },
            loading: false,
            dataList: [],
            amendmentVisible: false,// 新增 弹窗
            bianjiVisible: false,// 编辑 弹窗
            listExamForPlanData: [], //查询所有试卷
            listQMSDeptData: [], //部门
            getExamPlanModelByIdData: [], //详情Data
            jobModellShow: false, //人员配置 弹窗
            jobList: [],
            isEditTwo: true,
        }
    }
    componentDidMount() {

        this.queryTableList();
        this.listExamForPlan()//查询所有试卷
        this.listQMSDept() //部门
        this.queryJobList()//人员表
    }
    // 查询试卷列表
    queryTableList = () => {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page,
        }
        Api.listExamPlanModelByQuery(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    dataList: res.data.list,
                    total: res.data.total
                },
                    message.success(res.message)
                )
            } else {
                message.waring(res.message)
            }
        })
    }
    //设备列表分页
    handleTableChange = (page) => {
        this.setState({
            page,
        }, () => {
            this.queryTableList()
        })
    }
    // 删除试卷    
    listExamForPlan = () => {
        Api.listExamForPlan({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listExamForPlanData: res.data
                })
            }
        })
    }

    listQMSDept = () => [
        Api.listQMSDept({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listQMSDeptData: res.data
                })
            }
        })
    ]
    handleSearch = (event) => {
        console.log(event.target.value)
    }
    //新增 打开弹窗
    handleAdd = () => {
        this.setState({
            amendmentVisible: true
        })
    }

    onChange = (e, dateString) => {
        this.setState({
            selectStartTime: dateString
        })
    }
    onChangeTwo = (e, dateString) => {
        console.log(e, dateString, '4444444444');
        this.setState({
            selectEndTime: dateString
        })
    }

    //新增 弹窗 点击确定
    maintenanceCreate = () => {
        this.props.form.validateFieldsAndScroll((err, values) => {
            console.log(values, '6666666666666666')
            if (true) {
                const { selectStartTime, selectEndTime } = this.state
                // const examDurationA = Number(values.examDuration)
                Api.insertExamPlanModel({
                    examId: values.examId,
                    examPlanName: values.examPlanName == undefined ? values.examPlanName : values.examPlanName.trim(),
                    examDuration: Number(values.examDuration == undefined ? values.examDuration : values.examDuration.trim()),
                    totalPoints: Number(values.totalPoints == undefined ? values.totalPoints : values.totalPoints.trim()),
                    passPoints: Number(values.passPoints == undefined ? values.passPoints : values.passPoints.trim()),
                    startDate: selectStartTime,
                    endDate: selectEndTime,
                    examInfo: values.examInfo == undefined ? values.examInfo : values.examInfo.trim(),
                    examContent: values.examContent == undefined ? values.examContent : values.examContent.trim(),
                    useDeptId: values.bizId,
                }, (res) => {
                    if (res.code === '200') {
                        this.setState({
                            amendmentVisible: false,
                        },
                            message.success("新增成功")
                        )
                        this.queryTableList()
                    }
                })
            }
        });
        // }
    }
    //新增 弹窗 点击取消
    maintenanceCancel = () => {
        this.setState({
            amendmentVisible: false,
        })
    }
    //预览
    handlePreview = (record) => {
        this.props.history.push(`/testManagement/testArrange/previewTwo?examId=${record.examId}`)
    }

    //启用
    handleOn = (record) => {
        Api.startExamPlanModel({
            examPlanModelId: record.examPlanModelId
        }, (res) => {
            if (res.code === "200") {
                message.success(res.message)
            }
            this.queryTableList()
        })
    }
    // 删除试卷
    handleDelete = (record) => {
        Api.deleteExamPlanModel({
            examPlanModelId: record.examId
        }, (res) => {
            if (res.code === "200") {
                message.success(res.message)
                this.queryTableList()
            } else {
                message.waring("删除失败")
            }
        })
    }

    //编辑 取消
    amendmentNo = () => {
        this.setState({
            bianjiVisible: false
        })
    }
    // 点击 打开 编辑
    handleTrunOn = (record) => {
        if (record.isStart === true) {
            message.success("考试安排已经启动，不能进行编辑")
        } else {
            this.setState({
                bianjiVisible: true,
                examPlanModelId: record.examPlanModelId
            })
            Api.getExamPlanModelById({
                examPlanModelId: record.examPlanModelId
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        getExamPlanModelByIdData: res.data
                    })
                }
            })
        }
    }
    saveFormRefRevise = (formRefRevise) => {
        this.formRefRevise = formRefRevise;
    }
    //编辑 确定
    amendmentCan = (examPlanModelId) => {
        const form = this.formRefRevise.props.form;
        form.validateFields((err, value) => {
            console.log(value, err, true, '88888888888888')
            if (err) {
                return;
            }
            Api.updateExamPlanModel({
                examPlanModelId: examPlanModelId,
                useDeptId: value.useDeptId.key,
                examId: value.examId.key,
                examDuration: Number(value.examDuration),
                examPlanName: value.examPlanName == undefined ? value.examPlanName : value.examPlanName.trim(),
                totalPoints: Number(value.totalPoints),
                passPoints: Number(value.passPoints),
                examInfo: value.examInfo == undefined ? value.examInfo : value.examInfo.trim(),
                examContent: value.examContent == undefined ? value.examContent : value.examContent.trim(),
                startDate: value.startDate._i,
                endDate: value.endDate._i,
            }, (res) => {
                if (res.code === '200') {
                    this.setState({
                        bianjiVisible: false,
                    },
                        message.success("修改成功")
                    )
                    this.queryTableList()
                }
            })
        });
    }
    // 查询人员列表
    queryJobList = () => {
        const { pageData } = this.state
        Api.pageListUser({
            pageSize: pageData.pageSize,
            pageNo: pageData.page
        }, (res) => {
            if (res.code === '200') {
                this.setState({
                    jobList: res.data.list,
                })
            }
        })
    }
    // 点击 人员配置 -- 显示弹框
    handleJobModelShow = (record) => {
        if (record.isStart === true) {
            message.success("考试安排已经启动，不能进行配置")
        } else {
            this.setState({
                jobModellShow: true,
                userDetail: record,
                isEditTwo: true
            })
        }
    }
    // 部门弹框--关闭
    handleJobCancle = () => {
        this.setState({
            jobModellShow: false,
            userDetail: {}
        })
    }
    // 部门弹框--确定部门设置
    handleJobOk = (jobIdList) => {
        const params = {
            examPlanModelId: this.state.userDetail.examPlanModelId,
            userIds: jobIdList
        }
        Api.staffConfig(params, (res) => {
            if (res.code === '200') {
                message.success('人员配置成功！');
                this.handleJobCancle();
                this.queryTableList()
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;

        const { breadcrumbData, dataList, loading, amendmentVisible,isEditTwo, listExamForPlanData, bianjiVisible, getExamPlanModelByIdData, listQMSDeptData, examPlanModelId, jobModellShow, jobList, userDetail, showSizeChanger, total } = this.state;
        console.log(getExamPlanModelByIdData, '111111111111')
        const columns = [
            {
                title: '试卷名称',
                dataIndex: 'examPlanName',
                key: 'examPlanName',
                render: (text, record, index) => {
                    if (record.examPlanName) {
                        return (
                            <div className='ellipsis'>{record.examPlanName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '使用部门',
                dataIndex: 'deptName',
                key: 'deptName',
                render: (text, record, index) => {
                    if (record.deptName) {
                        return (
                            <div className='ellipsis'>{record.deptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '编制者',
                dataIndex: 'createUserName',
                key: 'createUserName',
                render: (text, record, index) => {
                    if (record.createUserName) {
                        return (
                            <div className='ellipsis'>{record.createUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '起始时间',
                dataIndex: 'startDate',
                key: 'startDate',
                render: (text, record, index) => {
                    if (record.startDate) {
                        return (
                            <div className='ellipsis'>{record.startDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '结束时间',
                dataIndex: 'endDate',
                key: 'endDate',
                render: (text, record, index) => {
                    if (record.endDate) {
                        return (
                            <div className='ellipsis'>{record.endDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '启用',
                dataIndex: 'status',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.isStart === false) {
                        return (
                            <div className='ellipsis'>否</div>
                        )
                    } if (record.isStart === true) {
                        return (
                            <div className='ellipsis'>启用</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                dataIndex: 'operation',
                width: 250,
                render: (text, record) =>
                    <div>
                        <a onClick={this.handleTrunOn.bind(this, record)} className="operation">编辑</a>
                        <a onClick={this.handleJobModelShow.bind(this, record)} className="operation">人员配置</a>
                        {
                            record.isStart === false ?
                                <a className="operation" onClick={this.handleOn.bind(this, record)}>启用</a>
                                :
                                null
                        }
                        <Popconfirm title="确定要删除该数据吗">
                            <a onClick={this.handleDelete.bind(this, record)} className="operation">删除</a>
                        </Popconfirm>
                        <a onClick={this.handlePreview.bind(this, record)} className="operation">预览</a>
                    </div>

            },
        ]
        const pagination = total > 10 ? {
            total: this.state.total,
            pageSize: this.state.pageSize,
            current: this.state.page,
            onChange: this.handleTableChange,
            showQuickJumper: true,
            showSizeChanger,
            onShowSizeChange: this.onShowSizeChange,
        } : false
        //查询所有试卷
        const listExamFor = listExamForPlanData ? listExamForPlanData.map(val => {
            return <Option key={val.examId} value={val.examId}>{val.examName}</Option>
        }) : null

        //部门
        const listQMS = listQMSDeptData ? listQMSDeptData.map(val => {
            return <Option key={val.bizId} value={val.bizId}>{val.deptName}</Option>
        }) : null
        return (
            <div className="testArrange">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 新增或搜索 */}
                    <div className="btn-search">
                        <Button onClick={this.handleAdd} type="primary">新增</Button>
                        <div className="search">
                            <span>试卷安排：</span>
                            <Input placeholder="请输入" onPressEnter={this.handleSearch.bind(this)} />
                        </div>
                    </div>
                    <div className="table-list">
                        <Table
                            columns={columns}
                            dataSource={dataList}
                            pagination={pagination}
                            key="bizId"
                        ></Table>
                    </div>
                    {/* 编辑 */}
                    <AddFormFromWrap
                        visible={bianjiVisible}
                        wrappedComponentRef={this.saveFormRefRevise}
                        onCancel={this.amendmentNo}
                        onCreate={this.amendmentCan.bind(this, examPlanModelId)}
                        getExamPlanModelByIdData={getExamPlanModelByIdData}
                        listQMS={listQMS}
                    >
                    </AddFormFromWrap>
                    {/* 人员配置 */}
                    <JobAuthorization
                        ref="DeptModel"
                        visible={jobModellShow}
                        data={jobList}
                        userDetail name={userDetail.dspName}
                        checkedList={userDetail.jobIdListStr}
                        handleCancel={this.handleJobCancle}
                        handleOk={this.handleJobOk}
                        isEditTwo={isEditTwo}
                    />
                    <Modal
                        visible={amendmentVisible}
                        title={'试卷信息-录入'}
                        okText="确定"
                        cancelText='取消'
                        onCancel={this.maintenanceCancel}
                        onOk={this.maintenanceCreate}
                        maskClosable={false}
                    >
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="试卷"
                                    >
                                        {getFieldDecorator('examId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listExamFor}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试时长"
                                    >
                                        {getFieldDecorator('examDuration', {
                                            rules: [
                                                {
                                                    required: true,
                                                    message: `请输入考试时长!`,
                                                    pattern: new RegExp(/^[0-9]*$/),
                                                    message: '只能输入数字'
                                                },
                                            ],
                                        })(
                                            <span>
                                                <Input placeholder="请输入" autoComplete='off' />
                                            </span>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试名称"
                                    >
                                        {getFieldDecorator('examPlanName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="使用部门"
                                    >
                                        {getFieldDecorator('bizId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listQMS}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="总分"
                                    >
                                        {getFieldDecorator('totalPoints', {
                                            rules: [
                                                {
                                                    required: true,
                                                    message: `请输入总分!`,
                                                    pattern: new RegExp(/^[0-9]*$/),
                                                    message: '只能输入数字'
                                                },
                                            ],
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="及格分数"
                                    >
                                        {getFieldDecorator('passPoints', {
                                            rules: [
                                                {
                                                    required: true,
                                                    message: `请输入及格分数!`,
                                                    pattern: new RegExp(/^[0-9]*$/),
                                                    message: '只能输入数字'
                                                },
                                            ],
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="开始日期"
                                    >
                                        {getFieldDecorator('startDate', {
                                        })(
                                            < DatePicker format={dateFormat} onChange={this.onChange} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="结束日期"
                                    >
                                        {getFieldDecorator('endDate', {
                                        })(
                                            <DatePicker format={dateFormat} onChange={this.onChangeTwo} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={18}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试说明"
                                    >
                                        {getFieldDecorator('examInfo', {
                                        })(
                                            <TextArea placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={18}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试内容"
                                    >
                                        {getFieldDecorator('examContent', {
                                        })(
                                            <TextArea placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>

                        </Form>
                    </Modal>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner