import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table, Tabs, Row, Col, message, Upload, Transfer, DatePicker } from 'antd'
import './index.less'
import Api from '../../../api/index';
import moment from 'moment'
const { Option } = Select;
const { TextArea } = Input; const { RangePicker } = DatePicker;
const { TabPane } = Tabs
const dateFormat = 'YYYY/MM/DD HH:mm:ss'

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                checkColumns: [],
                listExamForPlanData: [], //查询所有试卷
            }
        }
        componentDidMount() {
            this.listExamForPlan()//查询所有试卷
        }

        listExamForPlan = () => {
            Api.listExamForPlan({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listExamForPlanData: res.data
                    })
                }
            })
        }
        render() {
            const {
                visible, onCancel, onCreate, form, getExamPlanModelByIdData, listQMS
            } = this.props;
            console.log(getExamPlanModelByIdData, '0000000000')
            const {
                listExamForPlanData
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };
            const startTime = moment(getExamPlanModelByIdData.startDate).format('YYYY/MM/DD HH:mm:ss');
            const endTime = moment(getExamPlanModelByIdData.endDate).format('YYYY/MM/DD HH:mm:ss');

            //查询所有试卷
            const listExamFor = listExamForPlanData ? listExamForPlanData.map(val => {
                return <Option key={val.examId} value={val.examId}>{val.examName}</Option>
            }) : null
            return (
                <Modal
                    visible={visible}
                    title={'试卷信息-编辑'}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form style={{ position: 'relative', top: -3 }}>
                        <Row>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="试卷"
                                >
                                    {getFieldDecorator('examId', {
                                        initialValue: getExamPlanModelByIdData ? { label: getExamPlanModelByIdData.examPlanName, key: getExamPlanModelByIdData.examId } : null,
                                    })(
                                        <Select
                                            labelInValue
                                            allowClear
                                            placeholder="请选择"
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {listExamFor}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="考试时长"
                                >
                                    {getFieldDecorator('examDuration', {
                                        initialValue: getExamPlanModelByIdData ? getExamPlanModelByIdData.examDuration : null
                                    })(
                                        <span>
                                            <Input placeholder="请输入" autoComplete='off' />
                                        </span>
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row style={{ position: 'relative', top: '-10px' }}>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="考试名称"
                                >
                                    {getFieldDecorator('examPlanName', {
                                        initialValue: getExamPlanModelByIdData ? getExamPlanModelByIdData.examPlanName : null
                                    })(
                                        <Input placeholder="请输入" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="使用部门"
                                >
                                    {getFieldDecorator('useDeptId', {
                                        initialValue: getExamPlanModelByIdData ? { label: null, key: getExamPlanModelByIdData.useDeptId } : null,
                                    })(
                                        <Select
                                            allowClear
                                            labelInValue
                                            placeholder="请选择"
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {listQMS}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row style={{ position: 'relative', top: '-10px' }}>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="总分"
                                >
                                    {getFieldDecorator('totalPoints', {
                                        initialValue: getExamPlanModelByIdData ? getExamPlanModelByIdData.totalPoints : null
                                    })(
                                        <Input placeholder="请输入" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="及格分数"
                                >
                                    {getFieldDecorator('passPoints', {
                                        initialValue: getExamPlanModelByIdData ? getExamPlanModelByIdData.passPoints : null
                                    })(
                                        <Input placeholder="请输入" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row style={{ position: 'relative', top: '-10px' }}>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="开始日期"
                                >
                                    {getFieldDecorator('startDate', {
                                        initialValue: getExamPlanModelByIdData ? moment(startTime, 'YYYY/MM/DD HH:mm:ss') : null,
                                    })(
                                        < DatePicker format={dateFormat} onChange={this.onChange} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="结束日期"
                                >
                                    {getFieldDecorator('endDate', {
                                        initialValue: getExamPlanModelByIdData ? moment(endTime, 'YYYY/MM/DD HH:mm:ss') : null,
                                    })(
                                        <DatePicker format={dateFormat} onChange={this.onChangeTwo} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row style={{ position: 'relative', top: '-10px' }}>
                            <Col span={18}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="考试说明"
                                >
                                    {getFieldDecorator('examInfo', {
                                        initialValue: getExamPlanModelByIdData ? getExamPlanModelByIdData.examInfo : null
                                    })(
                                        <TextArea placeholder="请输入" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row style={{ position: 'relative', top: '-10px' }}>
                            <Col span={18}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="考试内容"
                                >
                                    {getFieldDecorator('examContent', {
                                        initialValue: getExamPlanModelByIdData ? getExamPlanModelByIdData.examContent : null
                                    })(
                                        <TextArea placeholder="请输入" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>

                    </Form>
                </Modal>
            );
        }
    }
);
export default App