import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table, Tabs, Row, Col, message, Upload, Transfer, Button } from 'antd'
import './index.less'
import Api from '../../../api/index';
import BreadcrumbLink from '../../../components/bread'
import { searchToParams } from '../../../utils/utils.js'
import RoleAuthorization from '../../../components/roleAuthorization'
import FromWrap from './form';
import FromWrapTwo from './formTwo';
import ExamForm from './exaMineForm';
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            const { bizId } = searchToParams();
            this.state = {
                fileList: [],
                bizId,
                rightData: [],
                leftData: [],
                checkColumns: [],
                selectedKeys: [],
                pageListLogInfoData: [],//流程列表
                Columns: [],
                examData: [], //人员名单
                departmentalVisible: false, //部门授权弹窗
                roleVisible: false,
                userDetail: [],
                roleList: [],
                pageListFile: {
                    pageSize: 10,
                    page: 1
                },
                pageListFileData: [],//文件附件列表
                selectByPrimaryKeyData: [],//基本信息
                examVisible: false, //委托审批 弹窗
                departmentalVisibleTwo: false,//角色授权 弹窗
                indeterminate: true,
                // checkAll: false,
                pageData: {
                    pageSize: 100,
                    page: 1
                },
                breadcrumbData: [
                    { name: '文件管理' },
                    { name: '文件报批' },
                    { name: '审批' }
                ],
            }
        }
        componentDidMount() {
            this.setState({
                checkColumns: [{
                    title: '文件编号',
                    key: 'fileNo',
                    width: '200px',
                    render: (text, record, index) => {
                        if (record.fileNo) {
                            return (
                                <div className='ellipsis'>{record.fileNo}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '文件名称',
                    key: 'fileName',
                    render: (text, record, index) => {
                        if (record.fileName) {
                            return (
                                <div className='ellipsis'>{record.fileName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '版本',
                    key: 'fileVersion',
                    render: (text, record, index) => {
                        if (record.fileVersion) {
                            return (
                                <div className='ellipsis'>{record.fileVersion}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '到达时间',
                    key: 'beginDate',
                    render: (text, record, index) => {
                        if (record.beginDate) {
                            return (
                                <div className='ellipsis'>{record.applyDeptName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '完成时间',
                    key: 'finshedDate',
                    render: (text, record, index) => {
                        if (record.finshedDate) {
                            return (
                                <div className='ellipsis'>{record.finshedDate}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '处理人',
                    key: 'approveUserName',
                    render: (text, record, index) => {
                        if (record.approveUserName) {
                            return (
                                <div className='ellipsis'>{record.approveUserName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '结果',
                    key: 'result',
                    render: (text, record, index) => {
                        if (record.result === 1) {
                            return (
                                <div className='ellipsis'>提交</div>
                            )
                        } if (record.result === 2) {
                            return (
                                <div className='ellipsis'>待处理</div>
                            )
                        } if (record.result === 1) {
                            return (
                                <div className='ellipsis'>拒绝</div>
                            )
                        } if (record.result === 1) {
                            return (
                                <div className='ellipsis'>ok</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '意见',
                    key: 'remark',
                    render: (text, record, index) => {
                        if (record.remark) {
                            return (
                                <div className='ellipsis'>{record.remark}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                },
                ]
            })
            this.selectByPrimaryKey()//基本信息
            this.pageListFileAttach()//文件附件列表
            this.pageListLogInfo()//流程记录
            this.pageListQMSRoles()//角色
        }
        //基本信息
        selectByPrimaryKey = () => {
            const { bizId } = this.state
            Api.selectByPrimaryKey({
                bizId: bizId,
            }, (res) => {
                if (res.code === '200') {
                    this.setState({
                        selectByPrimaryKeyData: res.data
                    })
                }
            })
        }
        //文件附件列表
        pageListFileAttach = () => {
            const { bizId } = this.state
            Api.pageListFileAttach({
                bizId: bizId
                // pageSize: pageListFile.pageSize,
                // pageNo: pageListFile.page,
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        pageListFileData: res.data
                    })
                }
            })
        }
        //流程记录
        pageListLogInfo = () => {
            const { bizId } = this.state
            Api.pageListLogInfo({
                fileId: bizId
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        pageListLogInfoData: res.data.list
                    })
                }
            })
        }
        callback = (key) => {

        }
        // 文件上传
        handleChange = (info) => {
            //把fileList拿出来
            let { fileList } = info;
            const status = info.file.status;
            if (status) {
                if (status === 'done') {
                    fileList = fileList.slice(-1);
                    if (fileList[0].response.code === '200') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.getManagementTable();
                        })
                    } else if (fileList[0].response.code === '500') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.getManagementTable();
                        })
                    } else if (fileList[0].response.code === '40001') {
                        message.warning(fileList[0].response.message);
                    }
                } else if (status === 'error') {
                    message.error(`${info.file.name} 上传失败`);
                }
                //重新设置state
                this.setState({ fileList });
            }
        }
        //导入
        beforeUpload = (file) => {
            let nameArr = file.name.split('.');
            let name = nameArr[nameArr.length - 1];
            // 校验 格式
            if (['xlsx', 'xls'].indexOf(name) === -1) {
                return;
            }
            const isLt = file.size / 1024 / 1024 < 100;
            if (!isLt) {
                message.error('文件过大');
            }
            return isLt;
        }
        handleChange = (nextTargetKeys, direction, moveKeys) => {
            const { rightDataName } = this.props;
            let _obj = {};
            _obj[rightDataName] = nextTargetKeys
            this.props.setData(_obj);
        };
        handleSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {
            this.setState({ selectedKeys: [...sourceSelectedKeys, ...targetSelectedKeys] });
        };
        //点击退回
        handleSendBack = () => {
            this.props.history.push('/fileManagement/documentApproval')
        }
        //点击 通过审批
        startFileApprovalTure = () => {
            const { bizId } = this.state
            Api.startFileApproval({
                bizId: bizId,
                submitType: 2,
            }, (res) => {

            })
        }

        //委托 审批
        handleExam = () => {
            const { pageData } = this.state
            this.setState({
                examVisible: true
            })
            Api.pageListUser({
                pageSize: pageData.pageSize,
                pageNo: pageData.page
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        examData: res.data.list
                    })
                }
            })
        }
        // 点击 取消 委托
        amendmentCan = () => {
            this.setState({
                examVisible: false,
                examData: []
            })
        }
        //点击 确定 审批
        amendmentNo = () => {
            const form = this.formRefRevise.props.form;
            form.validateFields((err, value) => {
                console.log(value, err, true, '88888888888888')
                if (err) {
                    return;
                }
                const { selectByPrimaryKeyData } = this.state
                Api.startFileApproval({
                    bizId: selectByPrimaryKeyData.bizId,
                    submitType: 6,
                    receiveUserId: value.bizId
                }, (res) => {
                    if (res.code === "200") {
                        this.setState({
                            examVisible: false,
                            examData: []
                        })
                    }
                    message.success(res.message)
                    this.selectByPrimaryKey()
                })
            })
        }
        saveFormRefRevise = (formRefRevise) => {
            this.formRefRevise = formRefRevise;
        }

        //通过审批
        handleOk = () => {
            const { selectByPrimaryKeyData } = this.state
            Api.startFileApproval({
                bizId: selectByPrimaryKeyData.bizId,
                submitType: 2,
            }, (res) => {
                if (res.code === "200") {
                    this.props.history.push('/fileManagement/documentApproval')
                }
                message.success(res.message)
                this.selectByPrimaryKey()
            })
        }

        //保存 审批
        handleTurn = () => {
            const { selectByPrimaryKeyData } = this.state
            Api.startFileApproval({
                bizId: selectByPrimaryKeyData.bizId,
                submitType: 1,
            }, (res) => {
                if (res.code === "200") {
                    this.props.history.push('/fileManagement/documentApproval')
                }
                message.success("保存成功！")
                this.selectByPrimaryKey()
            })
        }
        //退回 审批
        handleOut = () => {
            const { selectByPrimaryKeyData } = this.state
            Api.startFileApproval({
                bizId: selectByPrimaryKeyData.bizId,
                submitType: 3,
            }, (res) => {
                if (res.code === "200") {
                    this.props.history.push('/fileManagement/documentApproval')
                }
                message.success("退回成功！")
                this.selectByPrimaryKey()
            })
        }

        //点击部门授权
        departmentalAuthorization = () => {
            this.setState({
                departmentalVisible: true,
            })
        }
        //部门授权 点击取消
        departmentalCan = () => {
            this.setState({
                departmentalVisible: false,
            })
        }
        //部门授权 点击确定
        departmentalNo = () => {
            const { selectByPrimaryKeyData } = this.state
            Api.addFileInDeptRole({
                fileId: selectByPrimaryKeyData.bizId,
                deptIdList: this.state.deptIdListData,
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        departmentalVisible: false,
                    })
                    message.success(res.message)
                }
            })
        }
        onCheckList = (checkedKeys) => {
            this.setState({
                checkedKeys,
                deptIdListData: checkedKeys,
            });
        };
        onSelectList = (selectedKeys, info) => {
            console.log('onSelect', info);
            this.setState({ selectedKeys });
        };
        // 数据循环
        changeData = (data) => {
            console.log(data, '的身份的事故发生的')
            data.map(item => {
                if (item.list && item.list.length) {
                    const val = item['list'];
                    delete item['list'];
                    item['children'] = val;
                    this.changeData(item.children);
                }
                return { ...item }
            })
            return data
        }

        // 查询角色列表
        pageListQMSRoles = () => {
            Api.pageListQMSRoles({}, (res) => {
                if (res.code === '200') {
                    const list = this.changeData(res.data.list);
                    this.setState({
                        roleList: list,
                    })
                }
            })
        }

        //点击角色授权 打开弹窗
        handleRole = ( selectByPrimaryKeyData ) => {
            this.setState({
                roleVisible: true,
                userDetail: selectByPrimaryKeyData ? selectByPrimaryKeyData : []
            })
        }
        // 角色授权取消弹框
        handleRoleCancle = () => {
            this.setState({
                roleVisible: false,
            })
        }
        // 角色授权确认弹框
        handleRoleOk = (checkedList) => {
            const params = {
                fileId: this.state.userDetail.bizId,
                roleIdList: checkedList
            }
            Api.addFileInDeptRole(params, (res) => {
                if (res.code === '200') {
                    message.success('角色授权成功！');
                    this.handleRoleCancle();
                }
            })
        }
        render() {
            const annexColumns = [{
                title: '文件名称',
                key: 'attachmentName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.attachmentName) {
                        return (
                            <div className='ellipsis'>{record.attachmentName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '格式',
                key: 'attachmentFormat',
                render: (text, record, index) => {
                    if (record.attachmentFormat) {
                        return (
                            <div className='ellipsis'>{record.attachmentFormat}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '上传时间',
                key: 'upLoadTime',
                render: (text, record, index) => {
                    if (record.upLoadTime) {
                        return (
                            <div className='ellipsis'>{record.upLoadTime}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            },]

            const Columns = [{
                title: '文件编号',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '版本',
                key: 'fileVersion',
                render: (text, record, index) => {
                    if (record.fileVersion) {
                        return (
                            <div className='ellipsis'>{record.fileVersion}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '到达时间',
                key: 'beginDate',
                render: (text, record, index) => {
                    if (record.beginDate) {
                        return (
                            <div className='ellipsis'>{record.beginDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '完成时间',
                key: 'finshedDate',
                render: (text, record, index) => {
                    if (record.finshedDate) {
                        return (
                            <div className='ellipsis'>{record.finshedDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '处理人',
                key: 'approveUserName',
                render: (text, record, index) => {
                    if (record.approveUserName) {
                        return (
                            <div className='ellipsis'>{record.approveUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '结果',
                key: 'remark',
                render: (text, record, index) => {
                    if (record.result === 1) {
                        return (
                            <div className='ellipsis'>提交</div>
                        )
                    } if (record.result === 2) {
                        return (
                            <div className='ellipsis'>待处理</div>
                        )
                    } if (record.result === 1) {
                        return (
                            <div className='ellipsis'>拒绝</div>
                        )
                    } if (record.result === 1) {
                        return (
                            <div className='ellipsis'>ok</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '意见',
                key: 'fileStatusName',
                render: (text, record, index) => {
                    if (record.fileStatusName) {
                        return (
                            <div className='ellipsis'>{record.fileStatusName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }]
            const {
                form
            } = this.props;
            const {
                leftData,
                rightData,
                selectedKeys,
                breadcrumbData,
                selectByPrimaryKeyData,
                checkColumns,
                pageListFileData,
                pageListLogInfoData,
                examData,
                examVisible,
                departmentalVisible,
                checkedKeys,
                departmentalVisibleTwo,
                roleVisible,
                roleList
            } = this.state;


            console.log(roleList, 'GGGGGGGGGGGGGGGGGGGG')
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 },
            };
            const uploadProps = {
                action: `/item/importCheckItem`,
                onChange: this.handleChange,
                multiple: false,
                showUploadList: false,
                beforeUpload: this.beforeUpload,
                headers: {
                    menuUrl: window.location.pathname
                }
            };

            return (
                <div className="wordForm">
                    <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                    {/* 一定要加p-content-wrapper */}
                    <div className='p-content-wrapper'>
                        <Form>
                            <Tabs defaultActiveKey="1" onChange={this.callback}>
                                <TabPane tab="基本信息" key="1">
                                    <div className="process-fundamental" style={{ position: 'relative', left: "-15px" }}>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">文件编号:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileNo}</div><div className="details-node" ><span style={{ float: 'right' }}>文件名称:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileName}</div><div className="details-node"><span style={{ float: 'right' }}>文件类别:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileTypeName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">版本号:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileVersion}</div><div className="details-node"><span style={{ float: 'right' }}>申请类别:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyTypeName}</div><div className="details-node"><span style={{ float: 'right' }}>审核人:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.approverUserName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">申请日期:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyDate}</div><div className="details-node"><span style={{ float: 'right' }}>页码:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.officePageSie}</div><div className="details-node"><span style={{ float: 'right' }}>申请部门:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyDeptName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">申请人:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyUserName}</div><div className="details-node"><span style={{ float: 'right' }}>申请原因:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyReasonName}</div><div className="details-node"><span style={{ float: 'right' }}>审批人:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.auditorName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">编写依据:</span></div><div style={{ float: 'left', width: '80%' }}>{selectByPrimaryKeyData.fileInfo}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">是否年审:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.isAnnualVerification === true ? "是" : "否"}</div><div className="details-node"><span style={{ float: 'right' }}>年审部门:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.annualVerificationDeptName}</div></div>
                                    </div>
                                    {/* 部门授权 */}
                                    <FromWrap
                                        style={{ width: '500px' }}
                                        visible={departmentalVisible}
                                        onCancel={this.departmentalCan}
                                        onCreate={this.departmentalNo}
                                        onCheck={this.onCheckList}
                                        onSelect={this.onSelectList}
                                        checkedKeys={checkedKeys}
                                    // depatId={depatId}
                                    />
                                    {/* 角色授权 */}
                                    <RoleAuthorization
                                        ref="RoleAuthorization"
                                        visible={roleVisible}
                                        data={roleList}
                                        handleCancel={this.handleRoleCancle}
                                        handleOk={this.handleRoleOk}
                                    />
                                    <ExamForm
                                        wrappedComponentRef={this.saveFormRefRevise}
                                        visible={examVisible}
                                        onCancel={this.amendmentCan}
                                        onCreate={this.amendmentNo}
                                        examData={examData}
                                    >
                                    </ExamForm>

                                </TabPane>
                                {/* <TabPane tab="文件内容" key="2">
                                </TabPane> */}
                                <TabPane tab="附件" key="2">
                                    <div className="addSkip">
                                        <Table
                                            columns={annexColumns}
                                            dataSource={pageListFileData}
                                            // pagination={pagination}
                                            key="bizId"
                                        ></Table>
                                        {/* <Upload {...uploadProps} fileList={this.state.fileList}>
                                            {<a style={{ marginRight: 20 }}>
                                                上传</a>}
                                        </Upload> */}
                                    </div>
                                </TabPane>
                                <TabPane tab="流程" key="3">
                                    <div style={{ paddingLeft: 33 }}>
                                        <Table
                                            columns={checkColumns}
                                            dataSource={pageListLogInfoData}
                                            // pagination={pagination}
                                            key="bizId"
                                        ></Table>
                                    </div>
                                </TabPane>
                                <TabPane tab="操作记录" key="4">
                                    <Table
                                        columns={Columns}
                                        dataSource={pageListLogInfoData}
                                        // pagination={pagination}
                                        key="bizId"
                                    ></Table>
                                </TabPane>
                            </Tabs>
                            <Col style={{ marginTop: "30px" }}>
                                <Button onClick={this.handleOk} type="primary">通过审批</Button>
                                <Button onClick={this.handleExam.bind(this, selectByPrimaryKeyData)} style={{ marginLeft: 20, }}>委托审批</Button>
                                <Button onClick={this.handleOut} style={{ marginLeft: 20, }}>退回</Button>
                                <Button onClick={this.handleTurn} style={{ marginLeft: 20, }}>保存</Button>
                                <Button onClick={this.departmentalAuthorization} type="primary" style={{ marginLeft: 20, }}>部门授权</Button>
                                <Button onClick={this.handleRole.bind(this, selectByPrimaryKeyData)} type="primary" style={{ marginLeft: 20, }}>角色授权</Button>
                                <Button onClick={this.handleSendBack} style={{ marginLeft: 20, }}>退出</Button>
                            </Col>
                        </Form>
                    </div>
                </div>
            );
        }
    }
);
export default App